import Editor, { Monaco } from "@monaco-editor/react";
import AddLinkIcon from "@mui/icons-material/AddLink";
import DeleteIcon from "@mui/icons-material/Delete";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  IconButton,
  Switch,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useRef, useState } from "react";

import { HStack } from "../common/stack";
import { TEditor } from "../editorModal/types";

export const ValueItem: React.FC<{
  label: string;
  value?: string | number;
  defaultValue?: string | number;
  targetKey: string;
  onClick: (evt) => void;
  onBlur: (evt) => void;
  onChange: (evt) => void;
  onDelete: () => void;
  isAttach: boolean;
}> = ({
  label,
  value,
  onClick,
  targetKey,
  onBlur,
  defaultValue,
  isAttach,
  onDelete,
  onChange,
}) => {
  const handleDelete = () => {
    onDelete();
  };

  return (
    <HStack direction="row" sx={{ flex: 1 }} spacing={2} alignItems="center">
      <Typography sx={{ marginRight: 0.1, minWidth: "45px", fontSize: 14 }}>
        {label}
      </Typography>
      {isAttach ? (
        <>
          <Button></Button>
          <IconButton onClick={handleDelete}>
            <DeleteIcon />
          </IconButton>
        </>
      ) : (
        <TextField
          defaultValue={defaultValue}
          value={value}
          onBlur={onBlur}
          sx={{ padding: "2px" }}
          variant="outlined"
          size="small"
          autoComplete="false"
          onChange={onChange}
        />
      )}
      <IconButton
        style={{ marginLeft: 0 }}
        onClick={onClick}
        data-key={targetKey}
      >
        <AddLinkIcon />
      </IconButton>
    </HStack>
  );
};

export const BoolItem: React.FC<{
  label: string;
  value?: boolean;
  defaultValue?: boolean;
  targetKey: string;
  onClick: (evt) => void;
  onChange: (evt) => void;
  onDelete: () => void;
  isAttach: boolean;
}> = ({
  label,
  value,
  onClick,
  targetKey,
  onChange,
  defaultValue,
  isAttach,
  onDelete,
}) => {
  const handleDelete = () => {
    onDelete();
  };

  return (
    <HStack direction="row" sx={{ flex: 1 }} spacing={2} alignItems="center">
      <Typography sx={{ marginRight: 0.1, minWidth: "45px", fontSize: 14 }}>
        {label}
      </Typography>
      {isAttach ? (
        <>
          <Button></Button>
          <IconButton onClick={handleDelete}>
            <DeleteIcon />
          </IconButton>
        </>
      ) : (
        <Switch
          checked={value}
          defaultChecked={defaultValue}
          onChange={onChange}
        />
      )}
      <IconButton
        style={{ marginLeft: 0 }}
        onClick={onClick}
        data-key={targetKey}
      >
        <AddLinkIcon />
      </IconButton>
    </HStack>
  );
};

export const ValueItemContainer: React.FC<{
  label: string;
  targetKey: string;
  onClick: (evt) => void;
  onDelete: () => void;
  isAttach: boolean;
  children?: React.ReactNode;
}> = ({ label, onClick, targetKey, isAttach, onDelete, children }) => {
  const handleDelete = () => {
    onDelete();
  };

  return (
    <HStack direction="row" sx={{ flex: 1 }} spacing={2} alignItems="center">
      <Typography sx={{ marginRight: 0.1, minWidth: "45px", fontSize: 14 }}>
        {label}
      </Typography>
      {isAttach ? (
        <>
          <Button></Button>
          <IconButton onClick={handleDelete}>
            <DeleteIcon />
          </IconButton>
        </>
      ) : (
        children
      )}
      <IconButton
        style={{ marginLeft: 0 }}
        onClick={onClick}
        data-key={targetKey}
      >
        <AddLinkIcon />
      </IconButton>
    </HStack>
  );
};

export const ObjectItem: React.FC<{
  title: string;
  value: any;
  onConfirm: (code: string) => void;
}> = ({ title, value, onConfirm }) => {
  const editorRef = useRef(null);
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleConfirm = () => {
    handleClose();
    onConfirm(editorRef.current.getValue());
  };

  const handleEditorDidMount = (editor: TEditor, monaco: Monaco) => {
    editorRef.current = editor;
    setTimeout(() => {
      handleFormat();
    }, 300);
  };

  const handleFormat = () => {
    editorRef.current.getAction("editor.action.formatDocument").run();
  };

  return (
    <>
      <Button onClick={handleOpen} fullWidth sx={{ flex: 1 }}>
        {title}
      </Button>
      <Dialog
        open={open}
        maxWidth="sm"
        fullWidth
        PaperProps={{ sx: { height: "50vh" } }}
        onClose={handleClose}
      >
        <DialogContent>
          <Editor
            onMount={handleEditorDidMount}
            height="100%"
            defaultLanguage="json"
            defaultValue={value ? JSON.stringify(value) : ""}
            options={{ minimap: { enabled: false } }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleFormat}></Button>
          <Button onClick={handleConfirm}></Button>
          <Button onClick={handleClose}></Button>
        </DialogActions>
      </Dialog>
    </>
  );
};
