import {
  ButtonGroup,
  Collapse,
  List,
  ListItemButton,
  ListItemText,
  Button,
  Dialog,
  DialogTitle,
  ListItem,
  DialogActions,
  DialogContent,
  ListSubheader,
  TextField,
  FormControlLabel,
  Checkbox,
  Input,
  Stack,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import { Entity, IVector3, Utils } from "@hc/engine";
import { IAnimationProps } from "./types";
import AddIcon from "@mui/icons-material/Add";

interface AnimationPanelProps {
  expand: () => void;
  isExpand: boolean;
  entity: Entity;
}

const AnimationPanel: React.FC<AnimationPanelProps> = ({
  expand,
  isExpand,
  entity,
}) => {
  const [open, setOpen] = useState(false);
  const [animations, setAnimations] = useState<IAnimationProps[]>([]);
  const [animationNames, setAnimationNames] = useState(entity.AnimationNames);
  const startAddAnimation = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setAnimations([]);
  };

  const addMoveAnimate = () => {
    setAnimations((ans) => [
      ...ans,
      { title: "", type: 0, data: [], group: false },
    ]);
  };
  const addRotateAnimate = () => {
    setAnimations((ans) => [
      ...ans,
      { title: "", type: 1, data: ["0", "0"], group: false },
    ]);
  };

  const handleChangePoint = (val: string, point: IVector3, k: string) => {
    point[k] = val;
    setAnimations([...animations]);
  };

  const handleAddPoint = (an: IAnimationProps) => {
    an.data.push({ x: "0", y: "0", z: "0" });
    setAnimations([...animations]);
  };

  const handleConfirm = () => {
    for (const an of animations) {
      if (an.type === 0 && an.data?.length > 0) {
        entity.moveAnimate({
          name: an.title,
          path: an.data.map((p) => ({
            x: Number(p.x) || 0,
            y: Number(p.y) || 0,
            z: Number(p.z) || 0,
          })),
        });
      } else if (an.type === 1) {
        if (an.data.some((d) => Number(d))) {
          entity.rotateAnimate({
            name: an.title,
            from: Utils.deg2rad(Number(an.data[0])),
            angle: Utils.deg2rad(Number(an.data[1])),
            time: 3,
            loop: true,
          });
        }
      }
    }
    setOpen(false);
  };

  const renderAnimationList = (an: IAnimationProps) => {
    switch (an.type) {
      case 0:
        return (
          <>
            {an.data.map((p, index) => (
              <ListItem key={index}>
                <TextField
                  label="x"
                  size="small"
                  value={p.x}
                  onChange={(e) => handleChangePoint(e.target.value, p, "x")}
                />
                <TextField
                  label="y"
                  size="small"
                  value={p.y}
                  onChange={(e) => handleChangePoint(e.target.value, p, "y")}
                />
                <TextField
                  label="z"
                  size="small"
                  value={p.z}
                  onChange={(e) => handleChangePoint(e.target.value, p, "z")}
                />
              </ListItem>
            ))}
            <ListItemButton onClick={() => handleAddPoint(an)}>
              <Button variant="outlined" endIcon={<AddIcon />}>
                
              </Button>
            </ListItemButton>
          </>
        );
      case 1:
        return (
          <ListItem>
            <TextField
              label=""
              size="small"
              value={an.data[0]}
              onChange={(e) => {
                an.data[0] = e.target.value;
                setAnimations([...animations]);
              }}
            />
            <TextField
              label=""
              size="small"
              value={an.data[1]}
              onChange={(e) => {
                an.data[1] = e.target.value;
                setAnimations([...animations]);
              }}
            />
          </ListItem>
        );
      case 2:
      default:
        return null;
    }
  };

  useEffect(() => {
    entity.on("addAnimation", () => {
      setAnimationNames(entity.AnimationNames);
    });
  }, []);

  return (
    <>
      <ListItemButton onClick={expand}>
        <ListItemText primary="" />
        {isExpand ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={isExpand} timeout="auto" unmountOnExit>
        <List
          component="div"
          disablePadding
          sx={{
            overflow: "auto",
            maxHeight: 300,
          }}
        >
          <ButtonGroup
            fullWidth
            orientation="vertical"
            aria-label="vertical outlined button group"
            sx={{
              "& button": {
                marginBottom: 1,
              },
            }}
          >
            {animationNames.map((nm) => (
              <Button
                variant="contained"
                color="primary"
                key={nm}
                onClick={() => entity.startAnimates(nm)}
              >
                {nm}
              </Button>
            ))}
            <Button color="primary" onClick={startAddAnimation}>
              
            </Button>
            <Button color="primary" onClick={() => entity.stopAnimates()}>
              
            </Button>
          </ButtonGroup>
        </List>
        <Dialog
          onClose={handleClose}
          open={open}
          fullWidth
          maxWidth="lg"
          PaperProps={{ sx: { height: "90vh" } }}
        >
          <DialogTitle></DialogTitle>
          <DialogContent sx={{ display: "flex", overflow: "hidden" }}>
            {animations.map((an, index) => (
              <List
                key={index}
                sx={{ pt: 0, width: "20%" }}
                subheader={
                  <ListSubheader component="div" id="nested-list-subheader">
                    <Stack direction="row">
                      <Input
                        value={an.title}
                        onChange={(e) => {
                          an.title = e.target.value;
                          setAnimations([...animations]);
                        }}
                      />
                      <FormControlLabel
                        control={<Checkbox />}
                        label=""
                        onChange={(e, checked) => {
                          an.group = checked;
                          setAnimations([...animations]);
                        }}
                      />
                    </Stack>
                  </ListSubheader>
                }
              >
                {renderAnimationList(an)}
              </List>
            ))}
          </DialogContent>
          <DialogActions>
            <Button variant="contained" onClick={addMoveAnimate}>
              
            </Button>
            <Button variant="contained" onClick={addRotateAnimate}>
              
            </Button>
            <Button variant="contained" onClick={handleConfirm}>
              
            </Button>
            <Button variant="contained" onClick={handleClose}>
              
            </Button>
          </DialogActions>
        </Dialog>
      </Collapse>
    </>
  );
};
export default AnimationPanel;
