import { ListItem, InputBase, InputLabel, FormControl } from "@mui/material";
import { alpha, styled } from "@mui/material/styles";
import React, { useState } from "react";
import { IDeviceDataOption } from "./index";
interface AliPanelProps {
  data: IDeviceDataOption;
  onChange: (d: IDeviceDataOption) => void;
}
const BootstrapInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    borderRadius: 4,
    position: "relative",
    border: "1px solid #ced4da",
    fontSize: 16,
    width: "auto",
    padding: "10px 12px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
    ]),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:focus": {
      boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
      borderColor: theme.palette.primary.main,
    },
  },
}));
const AliPanel: React.FC<AliPanelProps> = ({ data, onChange }) => {
  return (
    <>
      <ListItem>
        <FormControl variant="standard">
          <InputLabel shrink htmlFor="accound">
            ali
          </InputLabel>
          <BootstrapInput
            id="accound"
            value={data.accountName}
            onChange={(e) => {
              let temp = { ...data, accountName: e.target.value };
              onChange(temp);
            }}
          />
        </FormControl>
      </ListItem>
      <ListItem>
        <FormControl variant="standard">
          <InputLabel shrink htmlFor="prod">
            
          </InputLabel>
          <BootstrapInput
            id="prod"
            value={data.prodName}
            onChange={(e) => {
              let temp = { ...data, prodName: e.target.value };
              onChange(temp);
            }}
          />
        </FormControl>
      </ListItem>
      <ListItem>
        <FormControl variant="standard">
          <InputLabel shrink htmlFor="device">
            
          </InputLabel>
          <BootstrapInput
            id="device"
            value={data.deviceName}
            onChange={(e) => {
              let temp = { ...data, deviceName: e.target.value };
              onChange(temp);
            }}
          />
        </FormControl>
      </ListItem>
      <ListItem>
        <FormControl variant="standard">
          <InputLabel shrink htmlFor="fun">
            
          </InputLabel>
          <BootstrapInput
            id="fun"
            value={data.funName}
            onChange={(e) => {
              let temp = { ...data, funName: e.target.value };
              onChange(temp);
            }}
          />
        </FormControl>
      </ListItem>
    </>
  );
};
export default AliPanel;
