import { ListItem } from "@mui/material";
import React, { useState } from "react";
import CommonSelect from "../../common/select";
import AliPanel from "./aliPanel";
import HistronPanel from "./histronPanel";

export interface IDeviceDataOption {
  source: number;
  accountId: number;
  accountName: string;
  prodId: string;
  prodName: string;
  deviceId: number;
  deviceName: string;
  deviceKey: string;
  deviceSecret: string;
  funId: number;
  funName: string;
  funDataType: string;
  key: string;
  ws: string;
}
interface DeviceDataPanelProps {
  data: IDeviceDataOption;
  onChange: (d: IDeviceDataOption) => void;
}
const changeSourceObj = {
  accountId: 0,
  accountName: "",
  prodId: "",
  prodName: "",
  deviceId: 0,
  deviceName: "",
  deviceKey: "",
  deviceSecret: "",
  funId: 0,
  funName: "",
  funDataType: "",
  funKey: "",
  wsUrl: "",
};
const DeviceDataPanel: React.FC<DeviceDataPanelProps> = ({
  data,
  onChange,
}) => {
  return (
    <>
      <ListItem>
        <CommonSelect
          label=""
          fullWidth
          value={data.source}
          onChange={(e) => {
            let temp = { source: Number(e.target.value), ...changeSourceObj };
            onChange(temp);
          }}
          options={[
            { label: "", value: 0 },
            // { label: "", value: 1 },
          ]}
        />
      </ListItem>
      {data.source === 0 ? (
        <HistronPanel data={data} onChange={onChange}></HistronPanel>
      ) : (
        <AliPanel data={data} onChange={onChange}></AliPanel>
      )}
    </>
  );
};
export default DeviceDataPanel;
