import { useState } from "react";

const getLocalValue = (key: string): string | null => {
  let v = localStorage.getItem(key);

  return v;
};

export function useLocalStorage<T = unknown>(
  initValue: T,
  key: string,
  parse: (v) => any = (v) => v
): [T, (v: T) => void, () => boolean] {
  const [state, setState] = useState<T>(
    parse(getLocalValue(key) as any) ?? initValue
  );

  const changeState = (v: T) => {
    setState(v);
    localStorage.setItem(key, v.toString());
  };

  const hasState = () => {
    return !!localStorage.getItem(key);
  };

  return [state, changeState, hasState];
}
