import CloseIcon from "@mui/icons-material/Close";
import {
  Box,
  Card,
  DialogActions,
  DialogContent,
  DialogTitle,
  Stack,
  Tab,
  Tabs,
} from "@mui/material";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import Slide from "@mui/material/Slide";
import { TransitionProps } from "@mui/material/transitions";
import { observer } from "mobx-react-lite";
import * as React from "react";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import DialogHeader from "../common/dialogHeader";
import { RNDComponent } from "../common/rnd";

import { a11yProps, TabPanel } from "../common/tabPanel";
import ActionDetail from "./actionDetail";
import ActionListPanel from "./actionList";
import { EntityIntTabs, PageIntTabs } from "./tabs";

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement;
  },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

interface InteractionPageProps {
  onClose: (e, reasion?: string) => void;
}

const InteractionPage: React.FC<InteractionPageProps> = ({ onClose }) => {
  const store = useInteractionStoresContext();

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    store.changeTabIndex(newValue);
  };

  const handleConfirm = () => {
    store.save();
    handleClose(null);
  };
  const handleClose = (evt, reason?) => {
    store.resetIndexs();
    store.UseReal = true;
    onClose(evt, reason);
  };

  const tabs = store.SelectEntity ? EntityIntTabs : PageIntTabs;

  return (
    <Dialog
      fullWidth
      maxWidth="xl"
      open={true}
      onClose={handleClose}
      TransitionComponent={Transition}
      PaperComponent={RNDComponent}
    >
      <DialogHeader
        onClose={handleClose}
        title=""
        className="draggable-dialog-title"
        sx={{ cursor: "move" }}
      />
      <DialogContent>
        <Box height="100%">
          <Box height={50}>
            <Tabs
              value={store.tabIndex}
              onChange={handleChange}
              aria-label=""
            >
              {tabs.map((t, index) => (
                <Tab
                  key={t.id}
                  label={t.label}
                  {...a11yProps(index)}
                  disabled={store.currentEventIndex === -1 && index === 1}
                />
              ))}
            </Tabs>
          </Box>
          <Stack
            flexDirection="row"
            component={Card}
            raised
            elevation={3}
            sx={{ height: "calc(100% - 50px)" }}
          >
            <Box width="30%" component={Card} overflow="auto">
              {tabs.map((t, index) => (
                <TabPanel
                  value={store.tabIndex}
                  key={index}
                  currentValue={index}
                >
                  {t.component}
                </TabPanel>
              ))}
            </Box>
            <Box flex={1} component={Card} overflow="auto">
              <ActionListPanel />
            </Box>
            <Box flex={1} component={Card}>
              <ActionDetail />
            </Box>
          </Stack>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button autoFocus onClick={handleClose}>
          
        </Button>
        <Button onClick={handleConfirm} autoFocus>
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};
export default observer(InteractionPage);
