import { WidgetValue } from "@hc/engine";
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  Divider,
  IconButton,
  List,
  ListItem,
} from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { App } from "~/intance";
import { CompareOptions, getConditionDescription } from "~/utils/interaction";

import DialogHeader from "../common/dialogHeader";
import LabelInput from "../common/labelInput";
import CommonSelect from "../common/select";
import { HStack } from "../common/stack";
import ValueTypeSelect from "./actionDetails/valueTypeSelect";

import AddCircleIcon from "@mui/icons-material/AddCircle";
import CancelIcon from "@mui/icons-material/Cancel";
import { getSelectKey, ValueOptions } from "./utils";
import { useRightStores } from "~/contexts/rightStoreContext";

interface ConditionPanelProps {
  onClose: () => void;
  open: boolean;
  subTitle: string;
  onConfirm: (id: string) => void;
}

const ConditionPanel: React.FC<ConditionPanelProps> = ({
  onClose,
  open,
  subTitle,
  onConfirm,
}) => {
  const store = useInteractionStoresContext();
  const { globalVarStore } = useRightStores();
  const condition = store.tempCondition;

  const handleAddCompare = (evt: React.MouseEvent<HTMLButtonElement>) => {
    const el = evt.currentTarget as HTMLButtonElement;

    const dataIndex = el.getAttribute("data-index");

    store.addNewCompare(dataIndex ? Number(dataIndex) : undefined);
  };
  const handleRemoveCompare = (evt: React.MouseEvent<HTMLButtonElement>) => {
    const el = evt.currentTarget as HTMLButtonElement;

    const dataIndex = el.getAttribute("data-index");

    store.removeCompare(Number(dataIndex));
  };

  const handleChangeName = (evt) => {
    store.updateState(() => {
      condition.name = evt.target.value;
    });
  };

  const handleConfirm = () => {
    const id = store.saveConditions();
    onConfirm(id);
    onClose();
  };

  const handleChangeValue = (widgetValue: WidgetValue, evt) => {
    store.updateState(() => {
      if (widgetValue.type === "entity") {
        widgetValue.value = App.intance.Database.getObjectId(evt.target.value);
      } else {
        widgetValue.value =
          typeof widgetValue.value === "boolean"
            ? Boolean(evt.target.value)
            : evt.target.value;
      }
    });
  };

  const isNeedBool = (value: WidgetValue) =>
    //@ts-ignore
    ValueOptions.find((v) => v.value === getSelectKey(value))?.bool;

  const handleChangeWidgetValue = (
    evt,
    value: WidgetValue,
    value2?: WidgetValue
  ) => {
    const values = evt.target.value.split("-");
    store.updateState(() => {
      value.type = values[0];
      value.entityKey = values[1];
      if (value2 && isNeedBool(value)) {
        value2.type = "value";
        value2.value = true;
      }
    });
  };

  return (
    <Dialog
      fullWidth
      maxWidth="md"
      open={open}
      onClose={onClose}
      PaperProps={{ sx: { height: "70%" } }}
    >
      <DialogHeader
        onClose={onClose}
        title={"-" + subTitle}
        sx={{ background: "#F6F7FB" }}
      />
      <DialogContent>
        <HStack justifyContent="space-between">
          <LabelInput
            label=""
            sx={{ flex: 1 }}
            value={condition.name}
            onChange={handleChangeName}
          />
          <CommonSelect
            options={[
              { label: "", value: 0 },
              { label: "", value: 1 },
            ]}
            value={condition.isAll ? 0 : 1}
            onChange={(evt) =>
              store.updateState(() => {
                //@ts-ignore
                condition.isAll = evt.target.value === 0;
              })
            }
          />
        </HStack>
        <Divider sx={{ my: 1 }} />
        <List>
          {condition.conditions.map((comp, index) => (
            <ListItem
              key={comp.id}
              sx={{
                "&>.MuiFormControl-root": {
                  flex: 1,
                },
              }}
            >
              <ValueTypeSelect
                widgetValue={comp.value1}
                onChange={(evt) => {
                  handleChangeWidgetValue(evt, comp.value1, comp.value2);
                }}
                onChangeValue={(evt) => handleChangeValue(comp.value1, evt)}
                variables={globalVarStore.variables}
                selectProps={{
                  fullWidth: true,
                  sx: { my: 1 },
                }}
              />
              <CommonSelect
                value={comp.type}
                options={CompareOptions}
                onChange={(evt) => {
                  store.updateState(() => {
                    comp.type = evt.target.value as any;
                  });
                }}
              />
              <ValueTypeSelect
                widgetValue={comp.value2}
                onChange={(evt) => {
                  handleChangeWidgetValue(evt, comp.value2);
                }}
                onChangeValue={(evt) => handleChangeValue(comp.value2, evt)}
                variables={globalVarStore.variables}
                selectProps={{
                  fullWidth: true,
                  sx: { my: 1 },
                }}
                bool={isNeedBool(comp.value1)}
              />
              <Box
                sx={{
                  width: 80,
                  display: "flex",
                  justifyContent: "flex-end",
                }}
              >
                <IconButton
                  color="primary"
                  data-index={index}
                  onClick={handleAddCompare}
                >
                  <AddCircleIcon />
                </IconButton>
                <IconButton
                  color="error"
                  data-index={index}
                  onClick={handleRemoveCompare}
                >
                  <CancelIcon />
                </IconButton>
              </Box>
            </ListItem>
          ))}
        </List>
        <Button variant="contained" onClick={handleAddCompare}>
          
        </Button>
        <Box>
          {getConditionDescription(condition.conditions, condition.isAll, 0)}
        </Box>
      </DialogContent>
      <DialogActions>
        <Button autoFocus onClick={onClose}>
          
        </Button>
        <Button onClick={handleConfirm} autoFocus>
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};
export default observer(ConditionPanel);
