import { CMD, Component } from "@hc/engine";
import { Box, Drawer, Switch, TextField } from "@mui/material";
import React, { useEffect, useState } from "react";
import { App } from "~/intance";

import DataBinding from "../DataBinding";
import { getTargetDataBind } from "../DataBinding/utils";
import { ValueItemContainer } from "./common";

const ValueList = ({ props, entity, onClick, onUnAttach }) => {
  const renderChild = (prop) => {
    if (prop.type === "boolean")
      return (
        <Switch
          checked={entity.data[prop.prop]}
          onChange={(e) => {
            CMD.CommandWrap(() => {
              entity.data[prop.prop] = e.target.checked;
            }, "");
          }}
        />
      );
    else {
      return (
        <TextField
          defaultValue={entity.data[prop.prop]}
          onBlur={(e) => {
            CMD.CommandWrap(() => {
              entity.data[prop.prop] = e.target.value;
            }, "");
          }}
          sx={{ padding: "2px" }}
          variant="outlined"
          size="small"
          autoComplete="false"
        />
      );
    }
  };

  return (
    <>
      {props.map((prop) => {
        return (
          <ValueItemContainer
            key={prop.prop}
            label={prop.name}
            onClick={onClick}
            targetKey={`data/${prop.prop}`}
            onDelete={() => onUnAttach(prop)}
            isAttach={!!prop.object}
          >
            {renderChild(prop)}
          </ValueItemContainer>
        );
      })}
    </>
  );
};

const ComponentDetail: React.FC<{ entity: Component }> = ({ entity }) => {
  const [openLink, setOpenLink] = useState(false);
  const [targetKey, setTargetKey] = useState("");
  const Com = entity.constructor;
  const [list, setList] = useState([]);

  const startLink = (evt: React.SyntheticEvent) => {
    const targetKey = evt.currentTarget.getAttribute("data-key");
    setTargetKey(targetKey);
    setOpenLink(true);
  };
  const endLink = () => {
    setOpenLink(false);
  };

  const handleConfirm = (db) => {
    setList((list) => {
      for (const l of list) {
        if (l.targetKey === targetKey) {
          l.object = db;
        }
      }
      return [...list];
    });
    endLink();
  };

  const handleUnAttach = (prop) => {
    if (prop.object) App.intance.Database.DataBindTable.remove(prop.object);
    prop.object = null;
    setList([...list]);
  };

  useEffect(() => {
    if (Com.Schema?.propertys) {
      const list = [];
      for (const prop of Com.Schema.propertys) {
        const dbi = getTargetDataBind(entity, `data/${prop.prop}`);
        list.push({ ...prop, object: dbi, targetKey: `data/${prop.prop}` });
      }
      setList(list);
    }
  }, []);

  return (
    <Box>
      <ValueList
        props={list}
        entity={entity}
        onClick={startLink}
        onUnAttach={handleUnAttach}
      />
      <Drawer anchor="left" open={openLink} onClose={endLink}>
        <DataBinding
          onClose={endLink}
          entity={entity}
          targetKey={targetKey}
          onConfirm={handleConfirm}
        />
      </Drawer>
    </Box>
  );
};
export default ComponentDetail;
