import { BaseDom, BaseGui, DataBind, Entity, IApiDataOption } from "@hc/engine";
import { Button, List, ListItem, Stack } from "@mui/material";
import axios, { Method } from "axios";
import React, { useState } from "react";
import { App } from "~/intance";

import CommonSelect from "../common/select";
import { HStack } from "../common/stack";
import ApiDataPanel, { IApiOption } from "./apiData";
import DeviceDataPanel, { IDeviceDataOption } from "./deviceData";
import { getTargetDataBind } from "./utils";
import { encryptStgy } from "./strategy";
interface DataBindingProps {
  onClose: () => void;
  entity: Entity | BaseGui | BaseDom;
  targetKey: string;
  onConfirm: (d: DataBind) => void;
}

let idIndex = 0;

const DataBinding: React.FC<DataBindingProps> = ({
  onClose,
  entity,
  targetKey,
  onConfirm,
}) => {
  const dbi = getTargetDataBind(entity, targetKey);
  const [source, setSource] = useState(() => dbi?.type ?? "1");
  //@ts-ignore
  const [apiData, setApiData] = useState<IApiOption>(() => {
    if (dbi && source === "2") {
      const data = dbi.data as IApiDataOption;
      return { ...data, useTime: !!data.time };
    } else {
      return {
        type: "GET",
        params: [],
        url: "http://cicd3d.gkiiot.com/test3D",
        time: "",
        useTime: false,
        filter: `function _filter(data){
          return data;
        }`,
      };
    }
  });
  // const [wsData, setWSData] = useState<Record<string, any>>({});
  const [wsData, setWSData] = useState<IDeviceDataOption>(() => {
    if (dbi && source === "1") {
      const data = dbi.data as IDeviceDataOption;
      return { ...data };
    } else {
      return {
        source: 0,
        accountId: 0,
        accountName: "",
        prodId: "",
        prodName: "",
        deviceId: 0,
        deviceName: "",
        deviceKey: "",
        deviceSecret: "",
        funId: 0,
        funName: "",
        funDataType: "",
        key: "",
        ws: "",
      };
    }
  });
  const handleConfirm = () => {
    let target = getTargetDataBind(entity, targetKey);

    if (!target) {
      target = new DataBind(entity.ObjectId, targetKey);
      App.intance.Database.DataBindTable.append(target);
      target.type = source;
    }
    if (source === "2") {
      //@ts-ignore
      target.data = { ...apiData, time: parseFloat(apiData.time) };
    } else {
      wsData.ws = getWsUrl();
      target.data = { ...wsData };
    }
    onConfirm(target);
  };
  const getWsUrl = () => {
    let type = wsData.source === 0 ? "histron" : "ali";
    let wsUrl = encryptStgy[type](wsData);
    return wsUrl;
  };
  const handleTestLink = () => {
    axios({
      method: apiData.type.toLowerCase() as Method,
      url: apiData.url,
      data:
        apiData.type === "POST"
          ? { ...apiData.params }
          : {
              params: apiData.params,
            },
    }).then((res) => {
      if (res.status === 200) {
        setApiData({ ...apiData, response: JSON.stringify(res.data) });
      }
    });
  };

  const handleChangeApiData = (
    evt: React.ChangeEvent<HTMLInputElement>,
    check?: boolean
  ) => {
    const name = evt.target.name;

    if (name) {
      setApiData({
        ...apiData,
        [name]: check === undefined ? evt.target.value : check,
      });
    }
  };

  const handleAddApiParam = () => {
    setApiData({
      ...apiData,
      params: [
        ...apiData.params,
        {
          key: "",
          value: "",
          type: "value",
          id: idIndex++,
        },
      ],
    });
  };
  const handleDeleteApiParam = (index: number) => {
    setApiData((data) => {
      const params = data.params;
      params.splice(index, 1);
      return {
        ...data,
        params: [...params],
      };
    });
  };

  const handleUpdateApiParam = (index, newParam) => {
    setApiData((data) => {
      const params = data.params;
      Object.assign(params[index], newParam);
      return {
        ...data,
        params: [...params],
      };
    });
  };

  return (
    <Stack sx={{ width: 300, height: "100%" }}>
      <List sx={{ flex: 1 }}>
        <ListItem>
          <CommonSelect
            label=""
            value={source}
            options={[
              { label: "", value: "1" },
              { label: "", value: "2" },
            ]}
            fullWidth
            onChange={(evt) => {
              setSource(evt.target.value as any);
              if (dbi?.type === "1") {
                setApiData({ ...apiData });
              } else {
                setWSData({
                  ...wsData,
                });
              }
            }}
          />
        </ListItem>
        {source === "1" ? (
          <DeviceDataPanel
            data={wsData}
            onChange={(params) => {
              setWSData({ ...params });
            }}
          />
        ) : (
          <ApiDataPanel
            data={apiData}
            onChange={handleChangeApiData}
            addParam={handleAddApiParam}
            deleteParam={handleDeleteApiParam}
            updateParam={handleUpdateApiParam}
          />
        )}
      </List>
      <HStack height={30} justifyContent="flex-end">
        <Button variant="contained" color="primary" onClick={handleConfirm}>
          
        </Button>
        <Button
          onClick={onClose}
          variant="contained"
          color="error"
          sx={{ mx: 1 }}
        >
          
        </Button>
        {source === "2" && (
          <Button onClick={handleTestLink} variant="contained" sx={{ mx: 1 }}>
            
          </Button>
        )}
      </HStack>
    </Stack>
  );
};
export default DataBinding;
