import { ListItem, InputBase, InputLabel, FormControl } from "@mui/material";
import { alpha, styled } from "@mui/material/styles";
import React, { useState } from "react";
import { IDeviceDataOption } from "./index";
import { rootStore } from "~/stores/RootStore";
import {
  getCloudauthList,
  getCloudauthSign,
  getDevList,
  getFunList,
  getProdList,
} from "~/apis/dataSourceReq";
import DataSelect from "~/components/DataBinding/deviceData/dataSelect";
interface HistronPanelProps {
  data: IDeviceDataOption;
  onChange: (d: IDeviceDataOption) => void;
}
const BootstrapInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    borderRadius: 4,
    position: "relative",
    border: "1px solid #ced4da",
    fontSize: 16,
    width: "100%",
    padding: "10px 12px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
    ]),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:focus": {
      boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
      borderColor: theme.palette.primary.main,
    },
  },
}));
const changeAccountObj = {
  prodId: "",
  prodName: "",
  deviceId: 0,
  deviceName: "",
  deviceKey: "",
  deviceSecret: "",
  funId: 0,
  funName: "",
  funDataType: "",
  funKey: "",
  wsUrl: "",
};
const changeProdObj = {
  deviceId: 0,
  deviceName: "",
  deviceKey: "",
  deviceSecret: "",
  funId: 0,
  funName: "",
  funDataType: "",
  funKey: "",
  wsUrl: "",
};

const HistronPanel: React.FC<HistronPanelProps> = ({ data, onChange }) => {
  /*  */
  const [openAccount, setOpenAccount] = useState(false);
  const [accounts, setAccounts] = useState([]);
  const getAccount = () => {
    let params = {
      projectId: rootStore.projectId,
      source: data.source,
    };
    getCloudauthList(params).then((res) => {
      setAccounts(res);
    });
    setOpenAccount(true);
  };
  const closeAccount = (value: number) => {
    if (value && value !== data.accountId) {
      setCurrentAccount(value);
    }
    setOpenAccount(false);
  };
  const setCurrentAccount = (id) => {
    const currentAccount = accounts.find((item) => item.id === id);
    data.accountId = currentAccount.id;
    data.accountName = currentAccount.name;
  };
  /*  */
  const [openProd, setOpenProd] = useState(false);
  const [prods, setProds] = useState([]);
  const getProd = async () => {
    let sign = await getCloudauthSign({ id: data.accountId });
    if (!sign) {
      return;
    }
    let params = {
      appKey: sign.appKey,
      timestamp: sign.timestamp,
      sign: sign.sign,
    };
    getProdList(params).then((res) => {
      setProds(res);
    });
    setOpenProd(true);
  };
  const closeProd = (value) => {
    if (value && value !== data.prodId) {
      setCurrentProd(value);
    }
    setOpenProd(false);
  };
  const setCurrentProd = (id) => {
    const currentProd = prods.find((item) => item.id === id);
    data.prodId = currentProd.id;
    data.prodName = currentProd.name;
  };
  /*  */
  const [openDevice, setOpenDevice] = useState(false);
  const [devices, setDevices] = useState([]);
  const getDevice = async () => {
    let sign = await getCloudauthSign({ id: data.accountId });
    if (!sign) {
      return;
    }
    let params = {
      appKey: sign.appKey,
      timestamp: sign.timestamp,
      sign: sign.sign,
      prodId: data.prodId,
    };
    getDevList(params).then((res) => {
      setDevices(res);
    });
    setOpenDevice(true);
  };
  const closeDevice = (value) => {
    if (value && value !== data.deviceId) {
      setCurrentDevice(value);
    }
    setOpenDevice(false);
  };
  const setCurrentDevice = (id) => {
    const currentDevice = devices.find((item) => item.id === id);
    data.deviceId = currentDevice.id;
    data.deviceName = currentDevice.name;
    data.deviceKey = currentDevice.deviceKey;
    data.deviceSecret = currentDevice.deviceSecret;
  };
  /*  */
  const [openFun, setOpenFun] = useState(false);
  const [funs, setFuns] = useState([]);
  const getFun = async () => {
    let sign = await getCloudauthSign({ id: data.accountId });
    if (!sign) {
      return;
    }
    let params = {
      appKey: sign.appKey,
      timestamp: sign.timestamp,
      sign: sign.sign,
      prodId: data.prodId,
    };
    getFunList(params).then((res) => {
      setFuns(res);
    });
    setOpenFun(true);
  };
  const closeFun = (value) => {
    if (value && value !== data.funId) {
      setCurrentFun(value);
    }
    setOpenFun(false);
  };
  const setCurrentFun = (id) => {
    const currentFun = funs.find((item) => item.id === id);
    data.funId = currentFun.id;
    data.funName = currentFun.name;
    data.key = currentFun.funKey;
    data.funDataType = currentFun.funDataType;
  };
  return (
    <>
      <ListItem>
        <FormControl variant="standard" sx={{ width: "100%" }}>
          <InputLabel shrink htmlFor="account">
            
          </InputLabel>
          <BootstrapInput
            id="account"
            value={data.accountName}
            onClick={getAccount}
          />
        </FormControl>
      </ListItem>
      <ListItem>
        <FormControl variant="standard" sx={{ width: "100%" }}>
          <InputLabel shrink htmlFor="prod">
            
          </InputLabel>
          <BootstrapInput id="prod" value={data.prodName} onClick={getProd} />
        </FormControl>
      </ListItem>
      {data.prodId && (
        <>
          <ListItem>
            <FormControl variant="standard" sx={{ width: "100%" }}>
              <InputLabel shrink htmlFor="device">
                
              </InputLabel>
              <BootstrapInput
                id="device"
                value={data.deviceName}
                onClick={getDevice}
              />
            </FormControl>
          </ListItem>
          <ListItem>
            <FormControl variant="standard" sx={{ width: "100%" }}>
              <InputLabel shrink htmlFor="fun">
                
              </InputLabel>
              <BootstrapInput id="fun" value={data.funName} onClick={getFun} />
            </FormControl>
          </ListItem>
        </>
      )}
      {openAccount && (
        <DataSelect
          title=""
          onClose={closeAccount}
          value={data.accountId}
          options={accounts}
        ></DataSelect>
      )}
      {openProd && (
        <DataSelect
          title=""
          onClose={closeProd}
          value={data.prodId}
          options={prods}
        ></DataSelect>
      )}
      {openDevice && (
        <DataSelect
          title=""
          onClose={closeDevice}
          value={data.deviceId}
          options={devices}
        ></DataSelect>
      )}
      {openFun && (
        <DataSelect
          title=""
          onClose={closeFun}
          value={data.funId}
          options={funs}
        ></DataSelect>
      )}
    </>
  );
};
export default HistronPanel;
