import * as React from "react";
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import Paper, { PaperProps } from "@mui/material/Paper";
import { styled } from "@mui/material/styles";
import Draggable from "react-draggable";

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  "& .MuiDialogContent-root": {
    padding: theme.spacing(2),
  },
  "& .MuiDialogActions-root": {
    padding: theme.spacing(1),
  },
}));

export interface DialogTitleProps {
  id: string;
  children?: React.ReactNode;
  onClose: () => void;
}

const BootstrapDialogTitle = (props: DialogTitleProps) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2, cursor: "move" }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};

const PaperComponent = (props: PaperProps) => {
  return (
    <Draggable
      handle="#draggable-dialog-title"
      cancel={'[class*="MuiDialogContent-root"]'}
    >
      <Paper {...props} />
    </Draggable>
  );
};

interface SModalProps {
  visible?: boolean;
  title?: string;
  size?: any;
  width?: number;
  children?: any;
  header?: any;
  footer?: any;
  fullWidth?: boolean;
  sx?: object;
  height?: string | number;
  hideFooter?: boolean;
  handleClose?: Function;
  handleOk?: Function;
}

const SModal: React.FC<SModalProps> = ({
  title,
  visible,
  size,
  children,
  header,
  footer,
  fullWidth,
  sx,
  height,
  hideFooter,
  handleOk,
  handleClose,
}) => {
  const closeMethod = () => {
    handleClose && handleClose();
  };

  const okMethod = () => {
    handleOk && handleOk();
  };
  return (
    <BootstrapDialog
      // onClose={closeMethod}
      open={visible}
      maxWidth={size}
      fullWidth={fullWidth}
      PaperComponent={PaperComponent}
      aria-labelledby="draggable-dialog-title"
    >
      {header ? (
        header
      ) : (
        <BootstrapDialogTitle id="draggable-dialog-title" onClose={closeMethod}>
          {title}
        </BootstrapDialogTitle>
      )}
      <DialogContent
        sx={{
          // minHeight: `${minHeight}px`,
          height: `${height ? height + "px" : "auto"}`,
        }}
        dividers
      >
        {children}
      </DialogContent>
      {hideFooter ? null : (
        <DialogActions>
          {footer ? (
            footer
          ) : (
            <>
              <Button onClick={closeMethod}></Button>
              <Button onClick={okMethod}></Button>
            </>
          )}
        </DialogActions>
      )}
    </BootstrapDialog>
  );
};

export default SModal;
