import { IconButton, Tooltip } from "@mui/material";
import { when } from "mobx";
import React, { useEffect } from "react";
import { useLocalStorage } from "~/hooks/useLocalStrorage";
import { App } from "~/intance";
import IconAli from "../common/iconAli";

interface LocalstorageBtnProps {
  iconName: string;
  targetKey: string;
  tip: string;
}

const LocalstorageBtn: React.FC<LocalstorageBtnProps> = React.forwardRef(
  ({ iconName, targetKey, tip }, ref) => {
    const [value, setValue, hasValue] = useLocalStorage(
      true,
      targetKey,
      (n) => n === "true"
    );

    const handleClick = () => {
      setValue(!App.intance.Viewer[targetKey]);
      App.intance.Viewer[targetKey] = !App.intance.Viewer[targetKey];
    };

    useEffect(() => {
      if (hasValue) {
        App.intance.Viewer[targetKey] = value;
      } else {
        when(
          () => !!App.intance,
          () => {
            setValue(App.intance.Viewer[targetKey]);
          }
        );
      }
    }, []);

    return (
      <Tooltip title={tip} placement="left">
        <IconButton onClick={handleClick} sx={{ height: "40px" }}>
          <IconAli name={iconName} />
        </IconButton>
      </Tooltip>
    );
  }
);
export default LocalstorageBtn;
