import React, { useState } from "react";
import { ListItemText, Stack, Switch, Typography, Slider } from "@mui/material";
import { App } from "~/intance";
import Title from "~/components/common/title";
interface SceneLightProps {}

const SceneLight: React.FC<SceneLightProps> = () => {
  const [intensity, setIntensity] = useState(0.5);

  const handleChangeIntensity = (e, newValue: number) => {
    setIntensity(newValue);
    App.intance.Viewer.sceneControl.EmissiveIntensity = newValue;
  };

  return (
    <>
      <Stack direction="row" sx={{ mt: 1 }}>
        <Title title="" mt={0.5} />
        <Slider
          size="small"
          value={intensity}
          aria-label="Small"
          valueLabelDisplay="auto"
          min={0}
          max={1}
          step={0.1}
          sx={{ width: 150 }}
          onChange={handleChangeIntensity}
        />
      </Stack>
    </>
  );
};
export default SceneLight;
