import React, { useReducer } from "react";
import Editor from "~/components/editor";
import { FullContainer } from "~/components/fullContainer";
import LeftPanel from "~/components/leftPanel";
import RightPanel from "~/components/rightPanel";
import { EditorContext, InitEditorValue } from "~/contexts/editor-context";
import { editorReducers } from "./../../reducers/editorReducers";

interface EditorProps {
  target: Record<string, unknown>;
}

const Index: React.FC<EditorProps> = () => {
  const [editorState, dispatch] = useReducer(editorReducers, InitEditorValue);

  return (
    <EditorContext.Provider
      value={{
        editorState,
        dispatch,
      }}
    >
      <FullContainer
        sx={{ display: "flex", height: "100%", overflow: "hidden" }}
        id="editor"
      >
        <LeftPanel />
        <Editor />
        <RightPanel />
      </FullContainer>
    </EditorContext.Provider>
  );
};
export default Index;
