import { IVariableOption, ObjectId } from "@hc/engine";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import ArrowRightIcon from "@mui/icons-material/ArrowRight";
import TreeView from "@mui/lab/TreeView";
import {
  Button,
  Checkbox,
  Input,
  List,
  MenuItem,
  Popover,
} from "@mui/material";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import { observer } from "mobx-react-lite";
import * as React from "react";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { useRightStores } from "~/contexts/rightStoreContext";
import useContextMenu from "~/hooks/useContextMenu";
import { EditorIntMode, IInteractionOption } from "~/stores/InteractionStore";
import { getConditionDescription } from "~/utils/interaction";
import BlurPopover from "../common/blurPopover";

import { StyledTreeItem } from "../common/customTree";
import ActionsTreeItem from "./actionsTreeItem";
import ConditionPanel from "./conditionPanel";
import { getEntityOptions } from "./utils";

interface ActionListItemProps {
  interaction: IInteractionOption;
  eventIndex: number;
}

const ActionListItem: React.FC<ActionListItemProps> = ({
  interaction,
  eventIndex,
}) => {
  const store = useInteractionStoresContext();
  const { globalVarStore } = useRightStores();
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(
    null
  );
  const [checked, setChecked] = React.useState<any[]>([]);
  const [open, setOpen] = React.useState(false);
  const dataRef = React.useRef({ isParam: false, hasPage: false });
  const [openMenu, closeMenu, MenuContainer] = useContextMenu(() => {});
  const [expanded, setExpanded] = React.useState(
    store.getActionAllIds(interaction)
  );

  const [search, setSearch] = React.useState("");

  const handleToggleExpand = (event, nodeIds) => {
    setExpanded(nodeIds);
  };

  const handleAddCondition = (evt) => {
    store.addNewCondition();
    setOpen(true);
  };

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    dataRef.current.isParam =
      (event.target as HTMLButtonElement).getAttribute("data-type") === "param";
    dataRef.current.hasPage =
      (event.target as HTMLButtonElement).getAttribute("data-page") === "1";
    setAnchorEl(event.currentTarget);

    if (store.CurrentAction) {
      setChecked(store.CurrentAction.data.map((d) => d.objectId));
    }
  };

  const handleMouseEnter = (event) => {
    // dataRef.current.isParam =
    //   (event.target as HTMLButtonElement).getAttribute("data-type") === "param";
    // dataRef.current.hasPage =
    //   (event.target as HTMLButtonElement).getAttribute("data-page") === "1";
    // if (store.CurrentAction)
    //   setChecked(store.CurrentAction.data.map((d) => d.objectId));
  };

  const handleClose = () => {
    store.NewAddActionId = "";
    setAnchorEl(null);
    setChecked([]);
    setSearch("");
  };

  const handleToggle = (value: ObjectId<any>) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
      store.addActionDataItem(value);
    } else {
      newChecked.splice(currentIndex, 1);
      store.removeActionDataItem(value);
    }

    setChecked(newChecked);

    if (store.mode === EditorIntMode.List && currentIndex === -1) {
      store.step = 3;
      handleClose();
    }
  };
  const handleToggleParam = (value: IVariableOption) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
      store.addParamItem(value.key);
    } else {
      newChecked.splice(currentIndex, 1);
      store.removeParamItem(value.key);
    }

    setChecked(newChecked);
  };

  const handleEditorCondition = () => {
    store.editorCondition();
    setOpen(true);
  };

  const handleDelete = () => {
    store.remove();
    closeMenu();
  };

  const renderPopoverContent = () => {
    if (dataRef.current.isParam) {
      let vars = globalVarStore.variables;
      if (search) {
        vars = vars.filter((v) => v.key.includes(search));
      }
      return vars.map((en) => (
        <ListItem key={en.key} disablePadding>
          <ListItemButton
            role={undefined}
            onClick={handleToggleParam(en)}
            dense
          >
            <ListItemIcon>
              <Checkbox
                edge="start"
                checked={checked.includes(en)}
                tabIndex={-1}
                disableRipple
              />
            </ListItemIcon>
            <ListItemText id={en.key} primary={en.key} />
          </ListItemButton>
        </ListItem>
      ));
    } else {
      let EntityOptions = getEntityOptions(dataRef.current.hasPage);
      if (search) {
        EntityOptions = EntityOptions.filter((opt) =>
          opt.label.includes(search)
        );
      }
      return EntityOptions.map((en) => (
        <ListItem key={en.value} disablePadding>
          <ListItemButton
            role={undefined}
            onClick={handleToggle(en.objectId)}
            dense
          >
            <ListItemIcon>
              <Checkbox
                edge="start"
                checked={checked.includes(en.objectId)}
                tabIndex={-1}
                disableRipple
                inputProps={{ "aria-labelledby": en.value }}
              />
            </ListItemIcon>
            <ListItemText id={en.value} primary={en.label} />
          </ListItemButton>
        </ListItem>
      ));
    }
  };

  const handleStep3Ref = (el: HTMLElement, id: string) => {
    if (store.CurrentAction?.id === id) {
      store.portalContainer3 = el;
    }
  };

  const renderList = () => {
    if (interaction.conditions.length > 0) {
      return interaction.conditions.map((cof, cofIndex) => (
        <StyledTreeItem
          key={cof.id}
          nodeId={cof.id.toString()}
          labelText={cof.name}
          onClick={() => {
            store.setAllIndex(undefined, undefined, cofIndex, eventIndex);
          }}
          onDoubleClick={handleEditorCondition}
          desc={getConditionDescription(cof.conditions, cof.isAll, cofIndex)}
        >
          <ActionsTreeItem
            newAddId={store.NewAddActionId}
            actions={cof.actions}
            onTogglePopover={handleClick}
            onEnter={handleMouseEnter}
            store={store}
            onRef={handleStep3Ref}
            onClick={(index, actIndex) => {
              store.setAllIndex(index, actIndex, cofIndex, eventIndex);
            }}
          />
        </StyledTreeItem>
      ));
    } else {
      return (
        <ActionsTreeItem
          actions={interaction.actions}
          onEnter={handleMouseEnter}
          onClick={(index, actIndex) => {
            store.setAllIndex(index, actIndex, undefined, eventIndex);
          }}
          store={store}
          onTogglePopover={handleClick}
          newAddId={store.NewAddActionId}
          onRef={handleStep3Ref}
        />
      );
    }
  };

  const handleBlur = (evt: React.FocusEvent) => {
    //@ts-ignore
    let el = evt.relatedTarget as HTMLElement;
    const rootEl = evt.currentTarget as HTMLElement;
    let isBlur = false;
    do {
      if (el === rootEl) {
        isBlur = true;
        break;
      }
      el = el?.parentElement;
    } while (el);

    if (!isBlur) {
      handleClose();
    }
  };

  const handleConfirmCondition = (id: string) => {
    setExpanded((ids) => [...ids, id.toString()]);
  };

  const handleRef = (el: HTMLElement) => {
    if (store.CurrentInteraction?.id === interaction.id) {
      store.portalContainer = el;
    }
  };

  return (
    <>
      <TreeView
        aria-label="gmail"
        defaultCollapseIcon={<ArrowDropDownIcon fontSize="large" />}
        defaultExpandIcon={<ArrowRightIcon fontSize="large" />}
        sx={{
          flexGrow: 1,
          overflowY: store.step === 0 ? "auto" : undefined,
          width: "100%",
        }}
        onContextMenu={openMenu}
        expanded={expanded}
        onNodeToggle={handleToggleExpand}
      >
        <StyledTreeItem
          nodeId={interaction.id.toString()}
          data-nodeid={interaction.type + interaction.id}
          labelText={interaction.label}
          ref={handleRef}
          sx={{ width: "100%", position: "relative" }}
          onClick={() =>
            store.setAllIndex(undefined, undefined, undefined, eventIndex)
          }
          endElement={<Button onClick={handleAddCondition}></Button>}
        >
          {renderList()}
        </StyledTreeItem>
      </TreeView>
      <BlurPopover
        open={Boolean(anchorEl)}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "left",
        }}
      >
        <Input
          placeholder=""
          value={search}
          onChange={(evt) => setSearch(evt.target.value)}
        />
        <List sx={{ height: 300, overflowY: "auto" }}>
          {renderPopoverContent()}
        </List>
      </BlurPopover>
      <ConditionPanel
        subTitle={interaction.label}
        open={open}
        onClose={() => setOpen(false)}
        onConfirm={handleConfirmCondition}
      />
      <MenuContainer>
        <MenuItem onClick={handleDelete}></MenuItem>
      </MenuContainer>
    </>
  );
};
export default observer(ActionListItem);
