import {
  Box,
  List,
  ListItemText,
  Slider,
  Stack,
  Switch,
  TextField,
  Typography,
} from "@mui/material";
import { when } from "mobx";
import { observer } from "mobx-react-lite";
import React, { useEffect, useState } from "react";
import { useStores } from "~/contexts/store-context";
import { App } from "~/intance";

import type { ColorResult } from "react-color";
import ColorPickInput from "~/components/common/colorPickInput";
import Title from "~/components/common/title";

interface DefaultSceneConfigProps {}

const DefaultSceneConfig: React.FC<DefaultSceneConfigProps> = () => {
  const [bgColor, setBgColor] = useState("");
  const handleChangeColor = (c: ColorResult) => {
    setBgColor(c.hex);
    App.intance.Viewer.sceneControl.Background = c.hex;
  };

  useEffect(() => {
    when(
      () => !!App.intance,
      () => {
        const app = App.intance;
        app.on("loadFile", () => {
          setBgColor(app.Viewer.sceneControl.Background);
        });
      }
    );
  }, []);

  return (
    <Stack direction="row" sx={{ mt: 2 }}>
      <Title title="" />
      <ColorPickInput color={bgColor} onChange={handleChangeColor} />
    </Stack>
  );
};
export default DefaultSceneConfig;
