import { Box, Button, Switch, TextField } from "@mui/material";
import React from "react";
import { BaseGui, Entity, BaseDom, CMD } from "@hc/engine";
import { HC2DDom } from "~/extension/HC2DDom";
import ColorPickInput from "../common/colorPickInput";
import { ColorResult } from "react-color";
import { debounceCallback } from "~/utils/time";
import { ObjectItem } from "./common";
import { message } from "~/utils/message";

interface ValueContentProps {
  prop: any;
  onUpdate: () => void;
  entity: Entity | BaseGui | BaseDom;
}

const CommonTypes = new Set([
  "POSITIVENUMBER",
  "NUMBER",
  "STRING",
  "ALIGN",
  "VALIGN",
  "IMAGE",
]);
const CommonObjectTypes = new Set(["OBJECT", "COLORARRAY", "OBJECTARRAY"]);

const ValueContent: React.FC<ValueContentProps> = ({
  prop,
  entity,
  onUpdate,
}) => {
  const handleChange = (e) => {
    prop.value = e.target.value;
    onUpdate();
  };

  const handleChangeEntity = (value) => {
    CMD.CommandWrap(() => {
      if (entity instanceof BaseGui || entity instanceof HC2DDom) {
        entity.setProp(prop.key, value);
      } else entity[prop.key] = value;
    }, "");
  };

  const handleChangeColor = (c: ColorResult) => {
    prop.value = c.hex;
    onUpdate();
    debounceCallback(() => {
      handleChangeEntity(c.hex);
    }, 300);
  };

  const handleConfirmObject = (code: string) => {
    try {
      const data = JSON.parse(code);
      handleChangeEntity(data);
    } catch (err) {
      message.error("");
    }
  };

  const handleChangeSwitch = (e) => {
    prop.value = e.target.checked;
    onUpdate();
    handleChangeEntity(prop.value);
  };

  const handleConfirm = (e) => {
    CMD.CommandWrap(() => {
      if (entity instanceof BaseGui || entity instanceof HC2DDom) {
        entity.setProp(prop.key, e.target.value);
      } else {
        entity[prop.key] = parseFloat(e.target.value);
      }
    }, "");
  };

  const type = prop.type.toUpperCase();

  if (type === "BOOLEAN")
    return (
      <Box flex={1} textAlign="right">
        <Switch checked={Boolean(prop.value)} onChange={handleChangeSwitch} />
      </Box>
    );
  else if (prop.type === "Color") {
    return (
      <ColorPickInput
        sx={{ flex: 1, width: "auto" }}
        color={prop.value}
        onChange={handleChangeColor}
      />
    );
  } else if (CommonTypes.has(type)) {
    return (
      <TextField
        value={prop.value}
        onChange={handleChange}
        onBlur={handleConfirm}
        sx={{ padding: "2px" }}
        variant="outlined"
        size="small"
        autoComplete="false"
      />
    );
  } else if (CommonObjectTypes.has(type)) {
    return (
      <ObjectItem
        title={prop.key}
        value={prop.value}
        onConfirm={handleConfirmObject}
      />
    );
  } else {
    return <Box>{type}</Box>;
  }
};
export default ValueContent;
