import { Box, Stack, Tab, Tabs, IconButton, Tooltip } from "@mui/material";
import { Observer } from "mobx-react-lite";
import React, { useState } from "react";
import { useEditorContext } from "~/contexts/editor-context";
import { RightStoreContext } from "~/contexts/rightStoreContext";
import { rightStore } from "~/stores/rightStore";
import Divider from "@mui/material/Divider";
import { a11yProps, TabPanel } from "../common/tabPanel";
import SceneCollection from "./SceneCollection";
import { EntityTabs, SceneTabs } from "./tabs";
import toolBar from "./toolBar";
import { ERightTab } from "./types";
import { styled } from "@mui/material/styles";
interface RightPanelProps {
  children?: React.ReactNode;
  dir?: string;
}
const CustomizedTab = styled(Tab)`
  min-height: 34px;
`;
const CustomizedTabs = styled(Tabs)`
  height: 34px;
  min-height: 34px;
  & .MuiTabs-indicator {
    background: #0084ff;
  }
  & .MuiTabs-flexContainer {
    height: 34px;
  }
`;
const RightPanel: React.FC<RightPanelProps> = (props) => {
  const [value, setValue] = useState(ERightTab.Basic);
  const { editorState } = useEditorContext();
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  const TabsConfig =
    editorState.entity || editorState.gui || editorState.dom
      ? EntityTabs
      : SceneTabs;

  return (
    <RightStoreContext.Provider value={rightStore}>
      <Observer>
        {() => (
          <Stack sx={{ width: 300 }} flexDirection="row">
            <Stack width={40} sx={{ boxShadow: "1px 0 0 0 #ccc" }}>
              {toolBar.map((t, index) => (
                <React.Fragment key={index}>
                  <Tooltip title={t.tip} placement="left">
                    {t.component || (
                      <IconButton onClick={t.onClick} sx={{ height: "40px" }}>
                        <t.icon name={t.iconName} />
                      </IconButton>
                    )}
                  </Tooltip>
                  {t.divider && (
                    <Divider
                      sx={{ width: "24px", ml: "8px", mt: "16px", mb: "16px" }}
                    />
                  )}
                </React.Fragment>
              ))}
            </Stack>
            <Stack
              sx={{ flex: 1, minWidth: 0, height: "100%" }}
              flexDirection="column"
            >
              <Box
                sx={{
                  flex: 1,
                  minHeight: 0,
                  overflowY: "auto",
                  overflowX: "hidden",
                  height: "100%",
                }}
              >
                <CustomizedTabs
                  value={
                    !TabsConfig.find((t) => t.id === value)
                      ? ERightTab.Basic
                      : value
                  }
                  // variant="scrollable"
                  // scrollButtons="auto"
                  onChange={handleChange}
                  aria-label="right-panel-tabs"
                  sx={{ borderColor: "divider" }}
                >
                  {TabsConfig.map((model) => (
                    <CustomizedTab
                      key={model.id}
                      value={model.id}
                      label={model.title}
                      {...a11yProps(model.id)}
                      sx={{ minWidth: "30px", pl: "8px", pr: "8px" }}
                    />
                  ))}
                </CustomizedTabs>
                {TabsConfig.map((cof) => (
                  <TabPanel
                    value={cof.id}
                    currentValue={
                      !TabsConfig.find((t) => t.id === value)
                        ? ERightTab.Basic
                        : value
                    }
                    key={cof.id}
                  >
                    {cof.component}
                  </TabPanel>
                ))}
              </Box>
              <SceneCollection />
            </Stack>
          </Stack>
        )}
      </Observer>
    </RightStoreContext.Provider>
  );
};
export default RightPanel;
