import { Paper, PaperProps } from "@mui/material";
import React from "react";
import { Rnd } from "react-rnd";

const MIN_WIDTH = 800;
const MIN_HEIGHT = 600;

export function RNDComponent(props: PaperProps) {
  const [state, setState] = React.useState({
    width: (0.9 * window.innerWidth).toString(),
    height: (0.9 * window.innerHeight).toString(),
    x: 0.05 * window.innerWidth,
    y: 0.05 * window.innerHeight,
  });

  return (
    <Rnd
      dragHandleClassName="draggable-dialog-title"
      cancel={'[class*="MuiDialogContent-root"]'}
      minWidth={MIN_WIDTH}
      minHeight={MIN_HEIGHT}
      maxHeight={0.9 * window.innerHeight}
      maxWidth={0.9 * window.innerWidth}
      size={{ width: state.width, height: state.height }}
      position={{ x: state.x, y: state.y }}
      onDragStop={(e, d) => {
        if (d.y < 0) d.y = 0;
        setState({ ...state, x: d.x, y: d.y });
      }}
      onResizeStop={(e, direction, ref, delta, position) => {
        setState({
          width: ref.style.width,
          height: ref.style.height,
          ...position,
        });
      }}
    >
      <Paper
        {...props}
        sx={{
          "&": {
            margin: 0,
            width: "100%",
            minWidth: MIN_WIDTH,
            minHeight: MIN_HEIGHT,
            height: "100%",
            maxHeight: "100%",
          },
        }}
      />
    </Rnd>
  );
}
