import {
  Box,
  FormControlLabel,
  Input,
  Radio,
  RadioGroup,
  Stack,
} from "@mui/material";
import React, { useState } from "react";
import { IEditorState, useEditorContext } from "~/contexts/editor-context";
import { UPDATE, UPDATE_NAME, UPDATE_OTHER } from "~/reducers/editorReducers";
import { uiVector2Vector } from "~/utils/adapter";
import Vector3Panel from "../common/vector3Panel";
import TitleInput from "~/components/common/titleInput";
import Title from "~/components/common/title";
import { CMD } from "@hc/engine";

interface GuiBasePanelProps {}

const DomBasePanel: React.FC<GuiBasePanelProps> = () => {
  const { editorState, dispatch } = useEditorContext();

  const dom = editorState.dom;

  const handleChangeTF = (state: Partial<IEditorState>) => {
    dispatch({ type: UPDATE, ...state });
  };

  const handleChangeDom = (key: string, value: string) => {
    if (value)
      CMD.CommandWrap(() => {
        dom[key] = value;
      }, "DOM");
  };
  const handleTransfromDom = (key: string, value: any) => {
    CMD.CommandWrap(() => {
      dom[key] = value;
    }, "DOM");
  };

  const handleChange = (evt, value) => {
    dispatch({
      type: UPDATE_OTHER,
      other: { ...editorState.other, [evt.target.name]: Number(value) },
    });
    dom[evt.target.name] = Number(value);
  };

  return (
    <Box
      sx={{
        width: "100%",
        maxWidth: 360,
        bgcolor: "background.paper",
        fontSize: "14px",
      }}
      component="nav"
      aria-labelledby="nested-list-subheader"
    >
      <Stack direction="row" sx={{ mt: 2 }}>
        <TitleInput
          title=""
          value={editorState.name}
          onChange={(e) =>
            dispatch({ type: UPDATE_NAME, name: e.target.value })
          }
          onBlur={(e) => {
            dom.Name = e.target.value;
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <TitleInput
          title=""
          value={editorState?.other?.width}
          onChange={(e) =>
            dispatch({
              type: UPDATE_OTHER,
              other: { ...editorState.other, width: e.target.value },
            })
          }
          onBlur={(e) => {
            handleChangeDom("Width", e.target.value);
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <TitleInput
          title=""
          value={editorState?.other?.height}
          onChange={(e) =>
            dispatch({
              type: UPDATE_OTHER,
              other: { ...editorState.other, height: e.target.value },
            })
          }
          onBlur={(e) => {
            handleChangeDom("Height", e.target.value);
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <Vector3Panel
          vector={editorState.position}
          onChange={(k: string, v: string) =>
            handleChangeTF({ position: { ...editorState.position, [k]: v } })
          }
          onBlur={() => {
            handleTransfromDom("Position", editorState.position);
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title="" mt={0.7} />
        <RadioGroup
          aria-labelledby=""
          name="HorAlign"
          value={editorState?.other?.HorAlign}
          onChange={handleChange}
          row
        >
          <FormControlLabel value={0} control={<Radio />} label="" />
          <FormControlLabel value={1} control={<Radio />} label="" />
          <FormControlLabel value={2} control={<Radio />} label="" />
        </RadioGroup>
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title="" mt={0.7} />
        <RadioGroup
          aria-labelledby=""
          name="VerAlign"
          value={editorState?.other?.VerAlign}
          onChange={handleChange}
          row
        >
          <FormControlLabel value={0} control={<Radio />} label="" />
          <FormControlLabel value={1} control={<Radio />} label="" />
          <FormControlLabel value={2} control={<Radio />} label="" />
        </RadioGroup>
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <Vector3Panel
          vector={editorState.rotation}
          onBlur={() => {
            handleTransfromDom("Rotation", parseFloat(editorState.rotation.x));
          }}
          onChange={(k: string, v: string) =>
            handleChangeTF({ rotation: { ...editorState.rotation, [k]: v } })
          }
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title="" mt={0.7} />
        <Vector3Panel
          vector={editorState.scale}
          onBlur={() => {
            handleTransfromDom("Scale", uiVector2Vector(editorState.scale));
          }}
          onChange={(k: string, v: string) =>
            handleChangeTF({ scale: { ...editorState.scale, [k]: v } })
          }
        />
      </Stack>
    </Box>
  );
};
export default DomBasePanel;
