import React from "react";
import GlobalVarButton from "./globalVarButton";
import { App } from "~/intance";
import IconAli from "../common/iconAli";
import { CameraType } from "@hc/engine";
import { message, ToasterKeys } from "~/utils/message";
import { Button } from "@mui/material";
import { rootStore } from "~/stores/RootStore";
import { fileStore } from "~/stores/fileStore";
import CloudServicesButton from "~/components/rightPanel/cloudServices/cloudServicesButton";
import UndoRedoBtn from "./undo";
import LocalstorageBtn from "./localStorageBtn";
import RefreshIcon from "@mui/icons-material/Refresh";
import DirectionsRunIcon from "@mui/icons-material/DirectionsRun";
import GroupBtn from "./groupBtn";
let index = 0;

export default [
  {
    id: index++,
    tip: "",
    icon: IconAli,
    iconName: "icon-3Dshuaxin",
    onClick: () => {
      if (!fileStore.currentFileName) return;
      fileStore.setLoadProcess(false);
      fileStore.load(fileStore.currentFileId);
    },
    divider: false,
  },
  {
    id: index++,
    tip: "",
    component: <UndoRedoBtn iconName="icon-3Dchexiao" isUndo />,
    divider: false,
  },
  {
    id: index++,
    tip: "",
    component: <UndoRedoBtn iconName="icon-3Dhuanyuan" isUndo={false} />,
    divider: false,
  },
  // {
  //   id: index++,
  //   tip: "",
  //   component: <GroupBtn iconName="icon-3Dwanggekai" />,
  //   divider: true,
  // },
  {
    id: index++,
    tip: "",
    icon: IconAli,
    iconName: "icon-3Dwanggekai",
    component: (
      <LocalstorageBtn
        iconName="icon-3Dwanggekai"
        targetKey="ShowGround"
        tip=""
      />
    ),
    divider: false,
  },
  {
    id: index++,
    tip: "",
    icon: IconAli,
    iconName: "icon-3Dzuobiaoxi",
    onClick: () => {
      App.intance.Viewer.ShowAxesViewer = !App.intance.Viewer.ShowAxesViewer;
    },
    component: (
      <LocalstorageBtn
        iconName="icon-3Dzuobiaoxi"
        targetKey="ShowAxesViewer"
        tip=""
      />
    ),
    divider: false,
  },
  {
    id: index++,
    tip: "",
    icon: IconAli,
    iconName: "icon-3Dzoulu",
    onClick: () => {
      if (App.intance.Viewer.cameraManager.Type === CameraType.ArcRotate) {
        if (ToasterKeys.has("freeCamera")) {
          return;
        }
        message.info("", {
          key: "freeCamera",
          action: (
            <Button onClick={() => message.close("freeCamera")}></Button>
          ),
          autoHideDuration: null,
        });
      } else App.intance.Viewer.cameraManager.Type = CameraType.ArcRotate;
    },
    divider: false,
  },
  {
    id: index++,
    tip: "",
    icon: IconAli,
    iconName: "icon-3Dtexiao2",
    onClick: () => {
      let particleDisplay = rootStore.particleDisplay;
      let _entitys = App.intance.Database.ParticleSystemSpace.Entitys;
      Array.isArray(_entitys) &&
        _entitys.forEach((element) => {
          if (particleDisplay === true) {
            element.stop();
          } else {
            element.start();
          }
        });
      rootStore.particleDisplay = !particleDisplay;
    },
    divider: false,
  },
  {
    id: index++,
    tip: `()`,
    icon: IconAli,
    iconName: "icon-3Dline-focusguanzhujiaodian-02",
    onClick: () => {
      App.intance.zoomAll();
    },
    divider: true,
  },
  {
    id: index++,
    tip: "",
    iconName: "icon-3Dchexiao",
    icon: IconAli,
    component: <GlobalVarButton />,
    divider: false,
  },
  {
    id: index++,
    tip: "",
    iconName: "icon-3Dmoxingshuju",
    icon: IconAli,
    component: <CloudServicesButton />,
    divider: true,
  },

  // {
  //   id: index++,
  //   tip: ``,
  //   icon: IconAli,
  //   iconName: "icon-3Dquanping",
  //   onClick: () => {
  //     CMD.commandManager.ExecCommand("MODEL2ZERO");
  //   },
  // },
  {
    id: index++,
    tip: "",
    icon: IconAli,
    iconName: "icon-3Dquanping",
    onClick: () => {
      // document.body.requestFullscreen();
      const isFull = !!document.fullscreenElement;
      if (!isFull) {
        let el = document.documentElement;
        el.requestFullscreen();
      } else {
        document.exitFullscreen();
      }
    },
    divider: false,
  },
  // {
  //   id: index++,
  //   tip: "(F11)",
  //   icon: IconAli,
  //   iconName: "icon-3DoneAone",
  //   onClick: () => {
  //     const isFull = !!document.fullscreenElement;
  //     if (isFull) {
  //       document.exitFullscreen();
  //     }
  //   },
  // },
];
