import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  IconButton,
  List,
  ListItem,
  TextField,
  Typography,
} from "@mui/material";
import { Observer } from "mobx-react-lite";
import React, { useRef, useState } from "react";
import { useStores } from "~/contexts/store-context";

import DialogHeader from "../common/dialogHeader";
import { HStack } from "../common/stack";
import CodeEditor from "../editorModal/codeEditor";
import { TEditor } from "../editorModal/types";
import defaultCode from "./defaultCode";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import { App } from "~/intance";
import * as HC from "@hc/engine";
import { getFunction } from "~/utils/component";
import { AppEvent } from "@hc/engine";

interface ScriptPanelProps {}

const ScriptPanel: React.FC<ScriptPanelProps> = () => {
  const { fileStore } = useStores();
  const [open, setOpen] = useState(false);
  const [name, setName] = useState("");
  const editorRef = useRef<TEditor>(null);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    fileStore.editorScriptId = 0;
    setOpen(false);
  };

  const handleTest = () => {
    for (const c of fileStore.codeList) {
      const f = getFunction(c.code)();
      f(App.intance, HC);
    }
    setTimeout(() => {
      AppEvent.trigger({ type: "loadComplete", target: null });
    }, 1000);
  };

  const getCode = (id) => {
    const sp = fileStore.codeList.find((s) => s.id === id);
    if (sp) {
      return sp.code;
    }
    return;
  };

  const edit = (sp) => {
    handleOpen();
    fileStore.editorScriptId = sp.id;
    // editorRef.current.setValue(sp.code);
    setName(sp.name);
  };

  const handleConfirm = () => {
    if (fileStore.editorScriptId) {
      fileStore.updateCodeScript({
        name,
        code: editorRef.current.getValue(),
        id: fileStore.editorScriptId,
      });
      fileStore.editorScriptId = 0;
    } else {
      fileStore.appendCodeScript({
        name,
        code: editorRef.current.getValue(),
        id: Date.now(),
      });
    }

    handleClose();
  };

  return (
    <Observer>
      {() => (
        <Box sx={{ width: "100%" }}>
          <List>
            {fileStore.codeList.map((sp) => (
              <ListItem
                sx={{ outline: "1px solid #ccc" }}
                key={sp.id}
                secondaryAction={
                  <>
                    <IconButton aria-label="delete" onClick={() => edit(sp)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton
                      aria-label="delete"
                      onClick={() => fileStore.deleteScript(sp.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </>
                }
              >
                {sp.name}
              </ListItem>
            ))}
          </List>
          <Button
            variant="contained"
            onClick={handleOpen}
            fullWidth
            sx={{ width: "100%" }}
          >
            
          </Button>
          {localStorage.getItem("DEBUGSCRIPT") && (
            <Button
              variant="contained"
              onClick={handleTest}
              fullWidth
              sx={{ width: "100%", mt: 2 }}
            >
              
            </Button>
          )}

          <Dialog
            open={open}
            maxWidth="lg"
            fullWidth
            PaperProps={{ sx: { height: "80vh" } }}
          >
            <DialogHeader title="" onClose={handleClose} />
            <DialogContent>
              <HStack alignItems="center">
                <Typography>:</Typography>
                <TextField
                  value={name}
                  onChange={(evt) => setName(evt.target.value)}
                  size="small"
                />
              </HStack>
              <CodeEditor
                updateProperyty={() => {}}
                getEditor={(e) => (editorRef.current = e)}
                onChange={() => {}}
                onMount={() => {}}
                code={getCode(fileStore.editorScriptId) ?? defaultCode}
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={handleConfirm}></Button>
              <Button onClick={handleClose}></Button>
            </DialogActions>
          </Dialog>
        </Box>
      )}
    </Observer>
  );
};
export default ScriptPanel;
