import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  RadioGroup,
  Button,
} from "@mui/material";
import Radio from "@mui/material/Radio";
import FormControlLabel from "@mui/material/FormControlLabel";
interface option {
  id: number;
  name: string;
}
interface DataSelectProps {
  title: string;
  value: number | string;
  onClose: (value?: number | string) => void;
  options: option[];
}

const DataSelect: React.FC<DataSelectProps> = ({
  onClose,
  value: valueProp,
  title,
  options,
}) => {
  const [value, setValue] = useState(valueProp);
  useEffect(() => {
    setValue(valueProp);
  }, [valueProp]);

  const handleCancel = () => {
    onClose();
  };

  const handleOk = () => {
    onClose(value);
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    let targetVal = (event.target as HTMLInputElement).value;
    if (typeof value === "number") {
      setValue(Number(targetVal));
    } else {
      setValue(targetVal);
    }
  };
  return (
    <Dialog
      open={true}
      sx={{ "& .MuiDialog-paper": { width: "80%", maxHeight: 435 } }}
      maxWidth="xs"
    >
      <DialogTitle>{title}</DialogTitle>
      <DialogContent dividers>
        <RadioGroup
          aria-label="ringtone"
          name="ringtone"
          value={value}
          onChange={handleChange}
        >
          {options.map((option) => (
            <FormControlLabel
              value={option.id}
              key={option.id}
              control={<Radio />}
              label={option.name}
            />
          ))}
        </RadioGroup>
      </DialogContent>
      <DialogActions>
        <Button autoFocus onClick={handleCancel}>
          
        </Button>
        <Button onClick={handleOk}></Button>
      </DialogActions>
    </Dialog>
  );
};
export default DataSelect;
