import { CMD, end } from "@hc/engine";
import { IconButton, Tooltip } from "@mui/material";
import { when } from "mobx";
import React, { useEffect, useState } from "react";
import { App } from "~/intance";
import IconAli from "../common/iconAli";

interface UndoBtnProps {
  isUndo: boolean;
  iconName: string;
}

const UndoRedoBtn: React.FC<UndoBtnProps> = React.forwardRef(
  ({ isUndo, iconName }: UndoBtnProps, ref: React.Ref<HTMLButtonElement>) => {
    const [disabled, setDisabled] = useState(true);
    const handleClick = () => {
      CMD.commandManager.ExecCommand(isUndo ? "UNDO" : "REDO");
    };

    const update = () => {
      if (isUndo) {
        setDisabled(!App.intance.Database.HistoryManager.HasUndo);
      } else {
        setDisabled(!App.intance.Database.HistoryManager.HasRedo);
      }
    };

    useEffect(() => {
      const clears = [];
      when(
        () => !!App.intance,
        () => {
          clears.push(
            end(
              App.intance.Database.HistoryManager,
              App.intance.Database.HistoryManager.endCmdEvent,
              () => {
                if (isUndo) setDisabled(false);
              }
            ),
            end(
              App.intance.Database.HistoryManager,
              App.intance.Database.HistoryManager.redoEvent,
              (e) => {
                update();
              }
            ),
            end(
              App.intance.Database.HistoryManager,
              App.intance.Database.HistoryManager.undoEvent,
              (e) => {
                update();
              }
            )
          );
        }
      );

      return () => {
        clears.forEach((c) => c());
      };
    }, []);

    return (
      <Tooltip title={isUndo ? "" : ""} placement="left">
        <IconButton
          onClick={disabled ? () => {} : handleClick}
          sx={{
            height: 40,
            cursor: disabled ? "not-allowed" : "pointer",
            color: disabled ? "rgba(0, 0, 0, 0.26)" : "rgba(0, 0, 0, 1)",
          }}
        >
          <IconAli
            name={iconName}
            color={disabled ? "rgba(0, 0, 0, 0.26)" : "rgba(0, 0, 0, 1)"}
          />
        </IconButton>
      </Tooltip>
    );
  }
);
export default UndoRedoBtn;
