import { Box, Button, Checkbox, Slider, Stack } from "@mui/material";
import { when } from "mobx";
import React, { useEffect, useState } from "react";
import Title from "~/components/common/title";
import Vector3Panel from "~/components/common/vector3Panel";
import { App } from "~/intance";
import { convert2UiData, uiVector2Vector } from "~/utils/adapter";

interface SunSceneConfigProps {}

const SunSceneConfig: React.FC<SunSceneConfigProps> = () => {
  const [sunConfig, setSunConfig] = useState({
    Turbidity: 0,
    Rayleigh: 0,
    Luminance: 0,
    Inclination: 0,
    Azimuth: 0,
  });

  const [sun, setSun] = useState({ x: "0", y: "0", z: "0" });

  const handleChange = (evt) => {
    setSunConfig({ ...sunConfig, [evt.target.name]: evt.target.value });
    App.intance.Viewer.sceneControl[evt.target.name] = evt.target.value;
  };

  const handleChangeSun = (k: string, v: string) => {
    setSun({ ...sun, [k]: v });
    App.intance.Viewer.sceneControl.SunPosition = uiVector2Vector({
      ...sun,
      [k]: v,
    });
  };

  const update = () => {
    const app = App.intance;
    setSunConfig({
      Turbidity: app.Viewer.sceneControl.Turbidity,
      Rayleigh: app.Viewer.sceneControl.Rayleigh,
      Luminance: app.Viewer.sceneControl.Luminance,
      Inclination: app.Viewer.sceneControl.Inclination,
      Azimuth: app.Viewer.sceneControl.Azimuth,
    });
    setSun(convert2UiData(app.Viewer.sceneControl.SunPosition));
  };

  useEffect(() => {
    when(
      () => !!App.intance,
      () => {
        const app = App.intance;
        update();
        app.on("loadFile", () => {
          update();
        });
      }
    );
  }, []);

  return (
    <Box>
      <Stack direction="row" sx={{ m: 0, p: 0 }}>
        <Title title="" mt={1.1} />
        <Slider
          size="small"
          value={sunConfig.Turbidity}
          aria-label="Small"
          valueLabelDisplay="auto"
          min={0}
          max={200}
          step={1}
          name="Turbidity"
          onChange={handleChange}
        />
      </Stack>
      <Stack direction="row" sx={{ m: 0, p: 0 }}>
        <Title title="" mt={1.1} />
        <Slider
          size="small"
          value={sunConfig.Rayleigh}
          aria-label="Small"
          valueLabelDisplay="auto"
          min={0}
          max={10}
          step={0.1}
          name="Rayleigh"
          onChange={handleChange}
        />
      </Stack>
      <Stack direction="row" sx={{ m: 0, p: 0 }}>
        <Title title="" mt={1.1} />
        <Slider
          size="small"
          value={sunConfig.Luminance}
          aria-label="Small"
          valueLabelDisplay="auto"
          min={0}
          max={1}
          step={0.1}
          name="Luminance"
          onChange={handleChange}
        />
      </Stack>
      {/* <Stack direction="row">
        <Title title="" mt={0.7} />
        <Vector3Panel
          sx={{ width: 50 }}
          vector={sun}
          onChange={handleChangeSun}
        />
      </Stack> */}
      <Stack direction="row" sx={{ m: 0, p: 0 }}>
        <Title title="" mt={1.1} />
        <Slider
          size="small"
          value={sunConfig.Inclination}
          aria-label="Small"
          valueLabelDisplay="auto"
          min={-0.5}
          max={0.5}
          step={0.01}
          name="Inclination"
          onChange={handleChange}
        />
      </Stack>
      <Stack direction="row" sx={{ m: 0, p: 0 }}>
        <Title title="" mt={1.1} />
        <Slider
          size="small"
          value={sunConfig.Azimuth}
          aria-label="Small"
          valueLabelDisplay="auto"
          min={0}
          max={1}
          step={0.1}
          name="Azimuth"
          onChange={handleChange}
        />
      </Stack>
    </Box>
  );
};
export default SunSceneConfig;
