import { Button, ButtonProps, Menu, MenuItem } from "@mui/material";
import React, { useState } from "react";

interface ButtonMenuListProps {
  title: string;
  options: { label: string; onClick: (close: () => void) => void }[];
  buttonProps?: ButtonProps;
}

const ButtonMenuList: React.FC<ButtonMenuListProps> = ({
  title,
  options,
  buttonProps = {},
}) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <Button onClick={handleClick} {...buttonProps}>
        {title}
      </Button>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
      >
        {options.map((opt, index) => (
          <MenuItem key={index} onClick={() => opt.onClick(handleClose)}>
            {opt.label}
          </MenuItem>
        ))}
      </Menu>
    </>
  );
};
export default ButtonMenuList;
