import {
  Box,
  Button,
  FormControlLabel,
  IconButton,
  InputAdornment,
  ListItem,
  ListItemButton,
  ListItemText,
  TextareaAutosize,
  TextField,
  Typography,
} from "@mui/material";
import React from "react";
import CommonSelect from "../common/select";
import Checkbox from "@mui/material/Checkbox";
import { HStack } from "../common/stack";
import ButtonMenuList from "../common/buttonMenuList";
import DeleteIcon from "@mui/icons-material/Delete";

export interface IApiOption {
  type: string;
  params: { type: string; value: string; key: string; id: number }[];
  url: string;
  time: string;
  filter: string;
  response?: string;
  useTime: boolean;
}

interface ApiDataPanelProps {
  data: IApiOption;
  onChange: (evt) => void;
  addParam: () => void;
  updateParam: (index: number, par) => void;
  deleteParam: (index: number) => void;
}

const ApiDataPanel: React.FC<ApiDataPanelProps> = ({
  data,
  onChange,
  addParam,
  updateParam,
  deleteParam,
}) => {
  return (
    <>
      <ListItem>
        <CommonSelect
          value={data.type}
          fullWidth
          options={["GET", "POST"]}
          label=""
        />
      </ListItem>
      <ListItem>
        <TextField
          fullWidth
          label=""
          size="small"
          value={data.url}
          name="url"
          onChange={onChange}
        />
      </ListItem>
      {data.params.map((par, index) => (
        <ListItem key={par.id}>
          <Box>
            <HStack justifyContent="space-between">
              <Typography>{index + 1}</Typography>
              <IconButton onClick={() => deleteParam(index)}>
                <DeleteIcon />
              </IconButton>
            </HStack>
            <TextField
              label=""
              size="small"
              sx={{ mb: 1 }}
              value={par.key}
              onChange={(evt) => updateParam(index, { key: evt.target.value })}
            />
            <HStack>
              <TextField
                sx={{ width: "70%" }}
                label=""
                size="small"
                value={par.value}
                onChange={(evt) =>
                  updateParam(index, { value: evt.target.value })
                }
              />
              <ButtonMenuList
                buttonProps={{ variant: "contained", size: "small" }}
                title=""
                options={[{ label: "", onClick: (close) => close() }]}
              />
            </HStack>
          </Box>
        </ListItem>
      ))}
      <ListItem>
        <Button variant="contained" onClick={addParam}>
          
        </Button>
      </ListItem>
      <ListItem>
        <ListItemText></ListItemText>
      </ListItem>
      <ListItem>
        <TextareaAutosize
          aria-label=""
          minRows={3}
          placeholder=""
          value={data.response}
          style={{ width: 300 }}
        />
      </ListItem>
      <ListItem>
        <ListItemText></ListItemText>
      </ListItem>
      <ListItem>
        <TextareaAutosize
          aria-label="filter"
          minRows={3}
          placeholder=""
          value={data.filter}
          name="filter"
          onChange={onChange}
          style={{ width: 300 }}
        />
      </ListItem>
      <ListItem>
        <FormControlLabel
          control={<Checkbox />}
          checked={data.useTime}
          onChange={onChange}
          name="useTime"
          label=""
          color="primary"
          sx={{ width: 200 }}
        />
        {data.useTime && (
          <TextField
            value={data.time}
            name="time"
            onChange={onChange}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start"></InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="start"></InputAdornment>
              ),
            }}
          />
        )}
      </ListItem>
    </>
  );
};
export default ApiDataPanel;
