import { IVector3, CameraType, Utils, end } from "@hc/engine";
import { Stack, Switch } from "@mui/material";
import { when } from "mobx";
import React, { useEffect, useState } from "react";
import { App } from "~/intance";
import { convert2UiData } from "~/utils/adapter";
import VectorPanel from "../common/vector3Panel";
import Title from "~/components/common/title";
import CommonSelect from "../common/select";
import { deg2rad, fixedNotZero, rad2Deg } from "~/utils/utils";
import TitleInput from "../common/titleInput";
import { debounceCallback } from "~/utils/time";
import { HStack } from "../common/stack";
interface CameraInfoProps {}

let timer: NodeJS.Timeout;

const CameraPanel: React.FC<CameraInfoProps> = () => {
  const [camera, setCamera] = React.useState({ x: "0", y: "0", z: "0" });
  const [target, setTarget] = React.useState({ x: "0", y: "0", z: "0" });
  const [mode, setMode] = useState(0);
  const [cameraType, setCameraType] = useState(CameraType.ArcRotate);
  const [cameraInfo, setCameraInfo] = useState({
    Alpha: "",
    Beta: "",
    Radius: "",
  });
  const [limit, setLimit] = useState({
    LowerRadiusLimit: "",
    UpperRadiusLimit: "",
    LowerAlphaLimit: "",
    UpperAlphaLimit: "",
    LowerBetaLimit: "",
    UpperBetaLimit: "",
    MinZ: "",
    MaxZ: "",
  });
  const [limitPan, setLimitPan] = useState({
    PanningAxis: { x: "", y: "", z: "" },
    OriginPanTarget: { x: "", y: "", z: "" },
    PanDistance: "",
  });

  const [enableTF, setEnableTF] = useState({
    EnableRotate: true,
    EnableZoom: true,
    EnablePan: true,
  });

  const handleChangeCameraVector = (k: string, v: string) => {
    const newValue = { ...camera, [k]: v };
    setCamera(newValue);
    if (isNaN(parseFloat(v))) return;
    App.intance.Viewer.cameraManager.Position = {
      x: parseFloat(newValue.x) || 0,
      y: parseFloat(newValue.y) || 0,
      z: parseFloat(newValue.z) || 0,
    };
  };
  const handleChangeTargetVector = (k: string, v: string) => {
    const newValue = { ...target, [k]: v };
    setTarget(newValue);
    if (isNaN(parseFloat(v))) return;
    App.intance.Viewer.cameraManager.Target = {
      x: parseFloat(newValue.x) || 0,
      y: parseFloat(newValue.y) || 0,
      z: parseFloat(newValue.z) || 0,
    };
  };

  const handleToggleCameraType = (evt) => {
    setMode(evt.target.value);
    App.intance.Viewer.cameraManager.Mode = Number(evt.target.value);
  };

  const handleChangeCameraInfo = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const key = evt.target.name;
    const value = evt.target.value;
    if (!key || !value) return;
    setCameraInfo({ ...cameraInfo, [key]: value });

    debounceCallback(() => {
      if (key === "Radius")
        App.intance.Viewer.cameraManager.Radius = parseFloat(value);
      else {
        App.intance.Viewer.cameraManager[key] = Utils.deg2rad(
          parseFloat(value)
        );
      }
    }, 200);
  };
  const handleChangeARCameraLimit = (
    evt: React.ChangeEvent<HTMLInputElement>
  ) => {
    const key = evt.target.name;
    const value = evt.target.value;
    if (!key) return;
    setLimit({ ...limit, [key]: value });

    debounceCallback(() => {
      if (key === "LowerRadiusLimit" || key === "UpperRadiusLimit")
        App.intance.Viewer.cameraManager.ArcRotateControl[key] = value
          ? parseFloat(value)
          : null;
      else if (key === "MinZ" || key === "MaxZ") {
        if (value)
          App.intance.Viewer.cameraManager.ArcRotateControl[key] =
            parseFloat(value);
      } else {
        App.intance.Viewer.cameraManager.ArcRotateControl[key] = deg2rad(value);
      }
    }, 500);
  };
  const handleToggleCameraTF = (
    evt: React.ChangeEvent<HTMLInputElement>,
    checked: boolean
  ) => {
    const key = evt.target.name;
    if (!key) return;
    setEnableTF({ ...enableTF, [key]: checked });

    debounceCallback(() => {
      App.intance.Viewer.cameraManager.ArcRotateControl[key] = checked;
    }, 500);
  };

  const update = () => {
    const app = App.intance;
    setCamera(
      convert2UiData(app.Viewer.cameraManager.Position as IVector3, (n) =>
        fixedNotZero(n, 2)
      )
    );
    setTarget(
      convert2UiData(app.Viewer.cameraManager.Target as IVector3, (n) =>
        fixedNotZero(n, 2)
      )
    );

    const Alpha = fixedNotZero(
      Utils.rad2Deg(app.Viewer.cameraManager.Alpha),
      2
    );
    const Beta = fixedNotZero(Utils.rad2Deg(app.Viewer.cameraManager.Beta), 2);
    const Radius = fixedNotZero(app.Viewer.cameraManager.Radius, 2);

    setCameraInfo({
      Alpha,
      Beta,
      Radius,
    });

    setMode(app.Viewer.cameraManager.Mode);

    setLimit({
      LowerRadiusLimit:
        app.Viewer.cameraManager.ArcRotateControl.LowerRadiusLimit?.toString?.() ??
        "",
      UpperRadiusLimit:
        app.Viewer.cameraManager.ArcRotateControl.UpperRadiusLimit?.toString?.() ??
        "",
      LowerAlphaLimit: rad2Deg(
        app.Viewer.cameraManager.ArcRotateControl.LowerAlphaLimit
      ),
      UpperAlphaLimit: rad2Deg(
        app.Viewer.cameraManager.ArcRotateControl.UpperAlphaLimit
      ),
      LowerBetaLimit: rad2Deg(
        app.Viewer.cameraManager.ArcRotateControl.LowerBetaLimit
      ),
      UpperBetaLimit: rad2Deg(
        app.Viewer.cameraManager.ArcRotateControl.UpperBetaLimit
      ),
      MinZ: app.Viewer.cameraManager.ArcRotateControl.MinZ,
      MaxZ: app.Viewer.cameraManager.ArcRotateControl.MaxZ,
    });
    setEnableTF({
      EnablePan: app.Viewer.cameraManager.ArcRotateControl.EnablePan,
      EnableRotate: app.Viewer.cameraManager.ArcRotateControl.EnableRotate,
      EnableZoom: app.Viewer.cameraManager.ArcRotateControl.EnableZoom,
    });
    setLimitPan({
      PanningAxis: convert2UiData(
        app.Viewer.cameraManager.ArcRotateControl.PanningAxis as IVector3,
        (n) => fixedNotZero(n, 2)
      ),
      OriginPanTarget: convert2UiData(
        app.Viewer.cameraManager.ArcRotateControl.OriginPanTarget as IVector3,
        (n) => fixedNotZero(n, 2)
      ),
      PanDistance:
        app.Viewer.cameraManager.ArcRotateControl.PanDistance?.toString?.() ??
        "",
    });
  };

  const handleChangePanVector = (
    k: string | React.ChangeEvent<HTMLInputElement>,
    v?: string,
    targetKey?: string
  ) => {
    if (typeof k !== "string") {
      setLimitPan({ ...limitPan, PanDistance: k.target.value });
      debounceCallback(() => {
        App.intance.Viewer.cameraManager.ArcRotateControl.PanDistance = k.target
          .value
          ? parseFloat(k.target.value)
          : null;
      }, 500);
    } else {
      const newValue = { ...limitPan[targetKey], [k]: v };
      setLimitPan({ ...limitPan, [targetKey]: newValue });
      if (isNaN(parseFloat(v))) return;
      App.intance.Viewer.cameraManager.ArcRotateControl[targetKey] = {
        x: parseFloat(newValue.x) || 0,
        y: parseFloat(newValue.y) || 0,
        z: parseFloat(newValue.z) || 0,
      };
    }
  };

  const renderContent = () => {
    if (cameraType === CameraType.ArcRotate) {
      return (
        <>
          <Stack direction="row" sx={{ mt: 2 }} title="">
            <TitleInput
              title=""
              value={cameraInfo.Alpha}
              onChange={handleChangeCameraInfo}
              name="Alpha"
            />
          </Stack>
          <Stack direction="row" title="" sx={{ mt: 2 }}>
            <TitleInput
              title=""
              value={cameraInfo.Beta}
              onChange={handleChangeCameraInfo}
              name="Beta"
            />
          </Stack>
          <Stack direction="row" title="" sx={{ mt: 2 }}>
            <TitleInput
              disabled={mode === 1}
              title=""
              value={cameraInfo.Radius}
              onChange={handleChangeCameraInfo}
              name="Radius"
            />
          </Stack>
        </>
      );
    } else {
      return (
        <>
          <Stack direction="row" sx={{ mt: 2 }}>
            <Title title="" mt={0.7} />
            <VectorPanel
              sx={{ width: 50 }}
              vector={camera}
              onChange={handleChangeCameraVector}
            />
          </Stack>
        </>
      );
    }
  };

  useEffect(() => {
    let disposes: (() => void)[] = [];
    when(
      () => !!App.intance,
      () => {
        update();
        const app = App.intance;
        disposes.push(
          app.on("CameraUpdate", (event) => {
            timer && clearTimeout(timer);
            timer = setTimeout(() => {
              update();
            }, 100);
          }),
          end(
            app.Viewer.cameraManager,
            app.Viewer.cameraManager.switchEvent,
            (c) => {
              setCameraType(c.Type);
            }
          )
        );
      }
    );
    return () => {
      timer && clearTimeout(timer);
      disposes.forEach((d) => d());
      disposes.length = 0;
    };
  }, []);

  return (
    <>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title="" mt={0.7} />
        <CommonSelect
          value={mode}
          onChange={handleToggleCameraType}
          disabled={cameraType !== CameraType.ArcRotate}
          options={[
            {
              label: "",
              value: 0,
            },
            {
              label: "",
              value: 1,
            },
          ]}
        />
      </Stack>
      {renderContent()}
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title="" mt={0.7} />
        <VectorPanel
          sx={{ width: 50 }}
          vector={target}
          onChange={handleChangeTargetVector}
        />
      </Stack>
      {cameraType === CameraType.ArcRotate && (
        <>
          <Stack direction="row" sx={{ mt: 1 }}>
            <Title title="" mt={1.1} />
            <Switch
              checked={enableTF.EnablePan}
              onChange={handleToggleCameraTF}
              name="EnablePan"
              inputProps={{ "aria-label": "controlled" }}
            />
          </Stack>
          <Stack direction="row" sx={{ mt: 1 }}>
            <Title title="" mt={1.1} />
            <Switch
              checked={enableTF.EnableRotate}
              onChange={handleToggleCameraTF}
              name="EnableRotate"
              inputProps={{ "aria-label": "controlled" }}
            />
          </Stack>
          <Stack direction="row" sx={{ mt: 1 }}>
            <Title title="" mt={1.1} />
            <Switch
              checked={enableTF.EnableZoom}
              onChange={handleToggleCameraTF}
              name="EnableZoom"
              inputProps={{ "aria-label": "controlled" }}
            />
          </Stack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="MinZ"
              title=""
              value={limit.MinZ}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="MaxZ"
              title=""
              value={limit.MaxZ}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="LowerRadiusLimit"
              title=""
              value={limit.LowerRadiusLimit}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="UpperRadiusLimit"
              title=""
              value={limit.UpperRadiusLimit}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="LowerAlphaLimit"
              title=""
              value={limit.LowerAlphaLimit}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="UpperAlphaLimit"
              title=""
              value={limit.UpperAlphaLimit}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="LowerBetaLimit"
              title=""
              value={limit.LowerBetaLimit}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="UpperBetaLimit"
              title=""
              value={limit.UpperBetaLimit}
              onChange={handleChangeARCameraLimit}
            />
          </HStack>

          <HStack sx={{ mt: 2 }}>
            <Title title="" mt={0.7} />
            <VectorPanel
              sx={{ width: 50 }}
              vector={limitPan.OriginPanTarget}
              onChange={handleChangePanVector}
              name="OriginPanTarget"
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <Title title="" mt={0.7} />
            <VectorPanel
              sx={{ width: 50 }}
              vector={limitPan.PanningAxis}
              onChange={handleChangePanVector}
              name="PanningAxis"
            />
          </HStack>
          <HStack sx={{ mt: 2 }}>
            <TitleInput
              name="PanDistance"
              title=""
              value={limitPan.PanDistance}
              onChange={handleChangePanVector}
            />
          </HStack>
        </>
      )}
    </>
  );
};
export default CameraPanel;
