import {
  Box,
  Checkbox,
  FormControlLabel,
  List,
  Slider,
  Stack,
  Switch,
} from "@mui/material";
import { when } from "mobx";
import { observer } from "mobx-react-lite";
import React, { useEffect, useState } from "react";
import { useStores } from "~/contexts/store-context";
import { App } from "~/intance";
import { message } from "~/utils/message";
import { debounceCallback } from "~/utils/time";

import ColorPickInput from "../common/colorPickInput";
import HcInput from "../common/hcInput";
import Title from "../common/title";
import BgType from "./scenePanel/bgType";
import SceneLight from "./scenePanel/sceneLight";

import type { ColorResult } from "react-color";
interface ScenePanelProps {}

const ScenePanel: React.FC<ScenePanelProps> = (props) => {
  const [shadowSwitch, setShadowSwitch] = useState(true);

  const { fileStore, rootStore } = useStores();

  const [hColor, setHColor] = useState("");
  const [aColor, setAColor] = useState("");
  const [hemInten, setHemInten] = useState(1);

  const [Reflect, setReflect] = useState(false);

  const [useGis, setUseGis] = useState(false);

  const handleChangeCurrentFileName = (e) => {
    // if (!e.target.value) return;
    fileStore.updateFileName(e.target.value);
  };

  let timer: NodeJS.Timeout;

  const handleChangeHemIntensity = (e, newValue: number) => {
    setHemInten(newValue);
    timer && clearTimeout(timer);
    timer = setTimeout(() => {
      App.intance.Viewer.hemisphericLight.Intensity = newValue;
    }, 100);
  };

  const handleChangeHeColor = (c: ColorResult) => {
    setHColor(c.hex);
    App.intance.Viewer.hemisphericLight.Color = c.hex;
  };
  const handleChangeAmColor = (c: ColorResult) => {
    setAColor(c.hex);
    App.intance.Viewer.sceneControl.AmbientColor = c.hex;
  };

  const toggleShadow = (e, checked: boolean) => {
    setShadowSwitch(checked);
    App.intance.IsShadow = checked;
  };
  const toggleGis = (evt, checked: boolean) => {
    setUseGis(checked);
    if (checked) {
      App.intance.openGis();
    } else {
      App.intance.closeGis();
    }
  };

  const update = () => {
    const app = App.intance;
    setHemInten(app.Viewer.hemisphericLight.Intensity);
    setHColor(app.Viewer.hemisphericLight.Color);
    setAColor(app.Viewer.sceneControl.AmbientColor);
    setShadowSwitch(app.IsShadow);
    setReflect(app.Viewer.sceneControl.IsReflect);
  };

  const handleSetIndex = (evt, checked: boolean) => {
    debounceCallback(() => {
      if (checked) fileStore.setAppIndex(fileStore.currentFileId);
      // postReq(AppApi.cancelPreview).then((res) => {
      //   rootStore.appIndexId = null;
      // });
      else message.warn(",");
    });
  };

  const handleRefl = (evt) => {
    setReflect(evt.target.checked);
    App.intance.Viewer.sceneControl.IsReflect = evt.target.checked;
  };

  useEffect(() => {
    let dispose: () => void;
    when(
      () => !!App.intance,
      () => {
        const app = App.intance;
        update();
        dispose = app.on("loadFile", () => {
          update();
        });
      }
    );
    return () => {
      dispose?.();
    };
  }, []);

  const styles = {
    inputRoot: {
      fontSize: 30,
    },
    labelRoot: {
      fontSize: 30,
      color: "red",
      "&$labelFocused": {
        color: "purple",
      },
    },
    labelFocused: {},
  };
  const onChangeName = (e) => {
    console.log(e);
  };
  return (
    <List
      sx={{
        width: "100%",
        maxWidth: 360,
        bgcolor: "background.paper",
        paddingTop: "0px",
      }}
      component="nav"
      aria-labelledby="nested-list-subheader"
    >
      <Box>
        <FormControlLabel
          checked={rootStore.appIndexId === fileStore.currentFileId}
          onChange={handleSetIndex}
          control={<Checkbox />}
          label=""
        />
        {/* <FormControlLabel
          checked={useGis}
          onChange={toggleGis}
          control={<Checkbox />}
          label=""
        /> */}
        <Stack direction="row">
          <HcInput
            title=""
            value={fileStore.currentFileName}
            onChange={handleChangeCurrentFileName}
          />
        </Stack>
        <BgType></BgType>
        <Stack direction="row" sx={{ mt: 1 }}>
          <Title title="" mt={1.1} />
          <Switch
            checked={shadowSwitch}
            onChange={toggleShadow}
            inputProps={{ "aria-label": "controlled" }}
          />
        </Stack>
        <Stack direction="row">
          <Title title="" mt={0.7} />
          <Switch checked={Reflect} onChange={handleRefl} />
        </Stack>
        <SceneLight></SceneLight>
        <Stack direction="row" sx={{ m: 0, p: 0 }} alignItems="center">
          <Title title="" mt={1.1} />
          <ColorPickInput color={aColor} onChange={handleChangeAmColor} />
        </Stack>
        <Stack direction="row" sx={{ m: 0, p: 0 }}>
          <Title title="" mt={1.1} />
          <Slider
            size="small"
            value={hemInten}
            aria-label="Small"
            valueLabelDisplay="auto"
            min={0}
            max={1}
            step={0.1}
            onChange={handleChangeHemIntensity}
          />
        </Stack>
        <Stack direction="row" sx={{ m: 0, p: 0 }} alignItems="center">
          <Title title="" mt={1.1} />
          <ColorPickInput color={hColor} onChange={handleChangeHeColor} />
        </Stack>
      </Box>
    </List>
  );
};
export default observer(ScenePanel);
