import {
  Dialog,
  DialogContent,
  Grid,
  ToggleButtonGroup,
  ToggleButton,
  TextareaAutosize,
  Button,
  TextField,
} from "@mui/material";
import DialogHeader from "../../common/dialogHeader";
import { useRightStores } from "~/contexts/rightStoreContext";
import { observer } from "mobx-react-lite";
import React, { useEffect, useState } from "react";
import STable from "~/components/STable";
import SModal from "~/components/SModal";
import { httpAction, getAction, deleteAction } from "~/apis/manage";
import { message } from "~/utils/message";
import { styled } from "@mui/material/styles";
import { rootStore } from "~/stores/RootStore";
import { CloudauthApi } from "~/apis";
import { reaction } from "mobx";

interface CloudServicesProps {}

const Input = styled(TextField)(({ theme }) => ({
  "& input": {
    height: "1em",
    width: "300px",
  },
}));

const SlefButton = styled(Button)(({ theme }) => ({
  marginRight: "8px",
}));

const Label = styled("div")(({ theme }) => ({
  display: "inline-block",
  width: "100px",
  fontSize: "14px",
  textAlign: "right",
}));

const formItemStyle = {
  display: "inline-flex",
  alignItems: "center",
  justifyContent: "center",
  height: "50px",
};

type FormData = {
  id?: string | number;
  name?: string;
  appKey?: string;
  appSecret?: string;
  remark?: string;
  type?: number; // 0  1
  projectId?: string | number;
  regionId?: string;
  aliId?: string;
  iotInstanceId?: string;
  groupId?: string;
};

let initFormData: FormData = {
  name: "",
  appKey: "",
  appSecret: "",
  remark: "",
  type: 0,
  projectId: "",
  regionId: "",
  aliId: "",
  iotInstanceId: "",
  groupId: "",
};
const CloudService: React.FC<CloudServicesProps> = () => {
  const [visible, setVisible] = useState(false);
  const [editTitle, setEditTitle] = useState("");
  const [formData, setFormData] = useState(initFormData);
  const [columns, setColumns] = useState([
    {
      key: "name",
      title: "",
      align: "left",
    },
    {
      key: "type",
      title: "",
      align: "center",
      render: (text, record, index) => (
        <span>{text === 0 ? "" : ""}</span>
      ),
    },
    {
      key: "remark",
      title: "",
      align: "center",
    },
    {
      key: "action",
      title: "",
      align: "center",
      render: (text, record, index) => (
        <>
          <SlefButton variant="outlined" onClick={() => edit(record)}>
            
          </SlefButton>
          <SlefButton
            variant="outlined"
            onClick={() => deleteData(record.id)}
            color="error"
            sx={{ marginRight: 0 }}
          >
            
          </SlefButton>
        </>
      ),
    },
  ]);
  const [dataSource, setDataSource] = useState([]);
  const [ipagenation, setIpagenation] = useState({
    pageNum: 0,
    pageSize: 5,
    pageSizeOptions: [1, 3, 5, 10, 25],
  });
  const [actionType, setActionType] = useState(false); // true false 
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  const [selectionRows, setSelectionRows] = useState([]);

  // 
  const add = () => {
    setFormData(initFormData);
    setEditTitle("");
    setVisible(true);
    setActionType(false);
  };

  const edit = (record) => {
    setFormData(record);
    setEditTitle("");
    setVisible(true);
    setActionType(true);
  };

  // 
  const handleOk = () => {
    let data = {
      ...formData,
      projectId: rootStore.projectId,
    };
    addOrEditData(data);
  };

  // 
  const changeValue = (event, type) => {
    let obj = { ...formData };
    obj[type] = event.target.value;
    setFormData(obj);
  };

  const changeBtnValue = (value, type) => {
    let obj = { ...formData };
    obj[type] = value;
    setFormData(obj);
  };

  // 
  const handleClose = () => {
    setVisible(false);
  };
  const handleCloudServicesClose = () => {
    cloudServicesStore.Open = false;
  };
  useEffect(() => {
    reaction(
      () => rootStore.projectId,
      () => {
        getDataList();
      }
    );
  }, []);
  const getDataList = () => {
    if (rootStore.projectId === 0) {
      return;
    }
    getAction(CloudauthApi.list, { project_id: rootStore.projectId })
      .then((res) => {
        if (res["code"] === 0) {
          let { list } = res.data;
          if (list && list.length > 0) {
            let arr = [];
            for (let item of list) {
              let obj = {
                id: item.id,
                name: item.name,
                appKey: item.app_key,
                appSecret: item.app_secret,
                remark: item.remark,
                type: item.type,
                projectId: item.project_id,
                regionId: item.region_id,
                aliId: item.ali_id,
                iotInstanceId: item.iot_instance_id,
                groupId: item.group_id,
                realm: item.realm,
              };

              arr.push(obj);
            }
            setDataSource(arr);
          } else {
            setDataSource([]);
          }
        }
      })
      .catch((err) => {
        console.log(err);
      });
    // }
  };
  const addOrEditData = (data) => {
    httpAction(CloudauthApi.edit, data, "post").then((res) => {
      if (res["code"] === 0) {
        getDataList();
        message.success("");
        setVisible(false);
      }
    });
  };

  const deleteData = (id) => {
    let formData = {
      id: [id],
    };
    deleteAction(CloudauthApi.delete, formData).then((res) => {
      if (res["code"] === 0) {
        getDataList();
        message.success("");
      }
    });
  };

  const deleteBatchData = () => {
    if (selectedRowKeys.length > 0) {
      let formData = {
        id: selectedRowKeys,
      };
      deleteAction(CloudauthApi.delete, formData).then((res) => {
        if (res["code"] === 0) {
          getDataList();
          message.success("");
        }
      });
    } else {
      message.error("");
    }
  };

  const handleChangePage = (page) => {
    setIpagenation({
      pageNum: page,
      pageSize: ipagenation.pageSize,
      pageSizeOptions: ipagenation.pageSizeOptions,
    });
  };

  const handleChangeRowsPerPage = (rowsPerPage) => {
    setIpagenation({
      pageNum: 0,
      pageSize: rowsPerPage,
      pageSizeOptions: ipagenation.pageSizeOptions,
    });
  };

  useEffect(() => {
    getDataList();
  }, []);
  useEffect(() => {
    reaction(
      () => rootStore.BgType,
      () => {
        getDataList();
      }
    );
  }, []);
  const selectedChange = (rowKeys, rows) => {
    setSelectedRowKeys(rowKeys);
    setSelectionRows(rows);
  };

  const { cloudServicesStore } = useRightStores();

  return (
    <Dialog
      open={cloudServicesStore.Open}
      maxWidth="md"
      PaperProps={{ sx: { height: "620px", minWidth: "620px" } }}
    >
      <DialogHeader onClose={handleCloudServicesClose} title="" />
      <DialogContent>
        <SlefButton
          variant="outlined"
          onClick={add}
          sx={{ marginBottom: "8px" }}
        >
          
        </SlefButton>
        <SlefButton
          variant="outlined"
          onClick={deleteBatchData}
          color="error"
          sx={{ marginRight: 0, marginBottom: "8px" }}
        >
          
        </SlefButton>
        <STable
          columns={columns}
          dataSource={dataSource}
          rowKey="id"
          ipagenation={ipagenation}
          handleChangePage={handleChangePage}
          handleChangeRowsPerPage={handleChangeRowsPerPage}
          selectedRowKeys={selectedRowKeys}
          selectionRows={selectionRows}
          selectedChange={selectedChange}
        ></STable>
        <SModal
          size="sm"
          title={editTitle}
          visible={visible}
          handleOk={handleOk}
          handleClose={handleClose}
          fullWidth={true}
        >
          <Grid container spacing={2}>
            <Grid item xs={12} sx={formItemStyle}>
              <Label></Label>
              <Input
                size="small"
                value={formData.name}
                onChange={(event) => {
                  changeValue(event, "name");
                }}
              ></Input>
            </Grid>
            <Grid item xs={12} sx={formItemStyle}>
              <Label></Label>
              <ToggleButtonGroup
                disabled={actionType}
                color="primary"
                value={formData.type}
                exclusive
                onChange={(event, value) => {
                  changeBtnValue(value, "type");
                }}
                sx={{ width: "326px", height: "30px" }}
              >
                <ToggleButton value={0}></ToggleButton>
                <ToggleButton value={1}></ToggleButton>
              </ToggleButtonGroup>
            </Grid>
            <Grid item xs={12} sx={formItemStyle}>
              <Label>appKey</Label>
              <Input
                disabled={actionType}
                size="small"
                value={formData.appKey}
                onChange={(event) => {
                  changeValue(event, "appKey");
                }}
              ></Input>
            </Grid>
            <Grid item xs={12} sx={formItemStyle}>
              <Label>app</Label>
              <Input
                disabled={actionType}
                size="small"
                value={formData.appSecret}
                onChange={(event) => {
                  changeValue(event, "appSecret");
                }}
              ></Input>
            </Grid>
            {formData.type === 1 ? (
              <>
                <Grid item xs={12} sx={formItemStyle}>
                  <Label></Label>
                  <Input
                    disabled={actionType}
                    size="small"
                    value={formData.regionId}
                    onChange={(event) => {
                      changeValue(event, "regionId");
                    }}
                  ></Input>
                </Grid>
                <Grid item xs={12} sx={formItemStyle}>
                  <Label>ID</Label>
                  <Input
                    disabled={actionType}
                    size="small"
                    value={formData.aliId}
                    onChange={(event) => {
                      changeValue(event, "aliId");
                    }}
                  ></Input>
                </Grid>
                <Grid item xs={12} sx={formItemStyle}>
                  <Label>ID</Label>
                  <Input
                    disabled={actionType}
                    size="small"
                    value={formData.iotInstanceId}
                    onChange={(event) => {
                      changeValue(event, "iotInstanceId");
                    }}
                  ></Input>
                </Grid>
                <Grid item xs={12} sx={formItemStyle}>
                  <Label>ID</Label>
                  <Input
                    disabled={actionType}
                    size="small"
                    value={formData.groupId}
                    onChange={(event) => {
                      changeValue(event, "groupId");
                    }}
                  ></Input>
                </Grid>
              </>
            ) : null}
            <Grid item xs={12} sx={{ ...formItemStyle, height: 70 }}>
              <Label></Label>
              <TextareaAutosize
                minRows={3}
                maxRows={3}
                style={{ width: 328 }}
                value={formData.remark}
                onChange={(event) => {
                  changeValue(event, "remark");
                }}
              />
            </Grid>
          </Grid>
        </SModal>
      </DialogContent>
    </Dialog>
  );
};
export default observer(CloudService);
