import { Box } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useEditorRootContext } from "~/contexts/editorRootContext";
import { LeftStoreContext } from "~/contexts/leftStoreContext";
import { useStores } from "~/contexts/store-context";
import { commonStore } from "~/stores/commonStore";

import { TabPanel } from "../common/tabPanel";
import tabs from "./tabs";
import { observer } from "mobx-react-lite";
import Resizable from "react-resizable-box";
import { app } from "../editor";
import { setPrefix } from "~/intance";
import { ELeftTab } from "./types";
import ApplyShopPanel from "../resourceManager/applyShop";
import { MarketApi } from "~/apis";
import { getReq, ResponseStatus } from "~/utils/axios";
import { MarkList } from "~/apis/types";
import {
  deleteQueryKey,
  updateLocationUrl,
  updateQueryStringParameter,
} from "~/utils/url";
import FileModal from "../sceneTab/fileModal";
import { message } from "~/utils/message";

const Resizable2 = Resizable as any;

const LeftPanel: React.FC = () => {
  const { fileStore, rootStore } = useStores();
  const { leftTabIndex, changeLeftTab } = useEditorRootContext();
  const [apply, setApply] = useState({ open: false, info: null });

  const handleEndApply = () => {
    setApply({ open: false, info: null });
    history.go(-2);
  };

  const handeSuccess = async (projectId) => {
    setApply({ open: false, info: null });
    updateLocationUrl("projectId", projectId);
    await rootStore.loadProject(Number(projectId));
    await Promise.all([commonStore.load()]);
  };

  const handleClose = () => {
    fileStore.closeModal();
  };
  const handleConfirm = (name: string) => {
    if (!name) {
      message.error("!");
      return false;
    }

    if (fileStore.selectFile.id) {
      return fileStore.updateFileName(name, fileStore.selectFile.id);
    } else {
      return fileStore.addFile(name).then((id) => {
        if (!id) return false;
        message.success("");
        const newUrl = updateQueryStringParameter(
          location.href,
          "id",
          id.toString()
        );
        history.replaceState(
          {
            path: newUrl,
          },
          "",
          newUrl
        );
        return true;
      });
    }
  };

  useEffect(() => {
    changeLeftTab(ELeftTab.Scene);
    const url = new URL(location.href);
    const projectId = url.searchParams.get("projectId");
    const id = url.searchParams.get("id");

    const code = url.searchParams.get("code");

    if (code) {
      getReq<MarkList>(MarketApi.detailEx, {
        params: { product_code: code },
      }).then((detailRes) => {
        if (detailRes.code === ResponseStatus.Ok) {
          const detail = detailRes.data;
          setApply({ open: true, info: detail });
        }
      });
      deleteQueryKey(location.href, "code", true);
    }

    (async () => {
      await setPrefix();
      if (projectId) {
        await rootStore.loadProject(projectId ? Number(projectId) : undefined);
        await Promise.all([commonStore.load()]);
        if (id) fileStore.load(Number(id));
      }
    })();
  }, []);

  return (
    <LeftStoreContext.Provider
      value={{
        commonStore: commonStore,
      }}
    >
      <Resizable2
        width={220}
        minWidth={220}
        maxWidth={440}
        enable={{
          top: false,
          right: true,
          bottom: false,
          left: false,
          topRight: false,
          bottomRight: false,
          bottomLeft: false,
          topLeft: false,
        }}
        onResizeStop={(e, direction, ref, d) => {
          app.Viewer.setSize();
        }}
      >
        <Box
          id="leftPanel"
          sx={{
            flexWrap: "wrap",
            minWidth: 220,
            display: "flex",
            maxWidth: 440,
            flex: 1,
            height: "100%",
            borderLeft: "1px solid #F2F2F2",
          }}
        >
          {tabs.map(
            (t) =>
              !t.isHide && (
                <TabPanel
                  value={leftTabIndex}
                  currentValue={t.id}
                  key={t.id}
                  style={{
                    flex: 1,
                    minWidth: 0,
                    height: "100%",
                    overflow: "auto",
                  }}
                >
                  {t.component}
                </TabPanel>
              )
          )}
        </Box>
      </Resizable2>
      <ApplyShopPanel
        open={apply.open}
        shop={apply.info}
        onClose={handleEndApply}
        commonStore={commonStore}
        fileStore={fileStore}
        needProject
        onSuccess={handeSuccess}
      />
      {fileStore.isOpenModal && (
        <FileModal
          onClose={handleClose}
          isUpdate={!!fileStore.selectFile.id}
          selectName={fileStore.selectFile.name}
          onConfirm={handleConfirm}
        />
      )}
    </LeftStoreContext.Provider>
  );
};
export default observer(LeftPanel);
