import { CompareType, WidgetValue } from "@hc/engine";
import { Box } from "@mui/material";
import React from "react";
import { IInteractionCondItem } from "~/stores/InteractionStore";

const getValueStr = (v: WidgetValue) => {
  let str = "";
  if (v.type === "entity") {
    str = v.value?.Object ? v.value?.Object?.["Name"] + "" : "[]";
  } else {
    str = v.value || "[]";
  }

  return str;
};
const getCompare = (type: CompareType) => {
  switch (type) {
    case CompareType.Equal:
      return "==";
    case CompareType.UnEqual:
      return "!=";
    case CompareType.Great:
      return ">";
    case CompareType.GtEqual:
      return ">=";
    case CompareType.Less:
      return "<";
    case CompareType.LessEqual:
      return "<=";
    default:
      return " ";
  }
};
const getStr = (cf: IInteractionCondItem) => {
  let str = "";

  str += getValueStr(cf.value1);
  str += " ";
  str += getCompare(cf.type);
  str += " ";
  str += getValueStr(cf.value2);

  return str;
};

export function getConditionDescription(
  conditions: IInteractionCondItem[],
  isAll: boolean,
  index: number
) {
  return (
    <Box>
      <span style={{ color: "#075FB8", marginRight: 10 }}>
        {index === 0 ? "If" : "Else If"}
      </span>
      {conditions.length === 0
        ? " "
        : conditions.map((v) => getStr(v)).join(isAll ? " && " : " || ")}
    </Box>
  );
}

export const CompareOptions = [
  { label: "", value: CompareType.Equal },
  { label: "", value: CompareType.UnEqual },
  { label: "", value: CompareType.Great },
  { label: "", value: CompareType.Less },
  { label: "", value: CompareType.GtEqual },
  { label: "", value: CompareType.LessEqual },
];
