import React from "react";
import {
  Collapse,
  ListItemButton,
  ListItemIcon,
  Button,
  ListItemText,
  Grid,
  Divider,
} from "@mui/material";
import { ExpandLess, ExpandMore } from "@mui/icons-material";
import GridViewIcon from "@mui/icons-material/GridView";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { observer } from "mobx-react-lite";

type listItemProps = {
  label: string;
  type?: string;
};

interface InteractionItemProps {
  data: any;
  isEvent: boolean;
}

const InteractionItem2: React.FC<InteractionItemProps> = observer(
  ({ data, isEvent }) => {
    const store = useInteractionStoresContext();

    const handleClick = (event: React.SyntheticEvent) => {
      const type = (event.currentTarget as HTMLElement).getAttribute(
        "data-type"
      );
      const d = data.list.find((l) => l.type === type);
      if (!d) return;
      if (isEvent) {
        store.changeTabIndex(1);
        store.addInteraction(d as any);
        store.step = 2;
      } else {
        store.addActionItem(d);
        store.step = 3;
      }
    };

    return (
      <>
        <Divider>{data.label}</Divider>
        {data.list.map((d) => (
          <ListItemButton
            disabled={isEvent && store.isUsedEvent(d.type)}
            data-type={d.type}
            onClick={handleClick}
            key={d.type}
          >
            <ListItemText primary={d.label} />
          </ListItemButton>
        ))}
      </>
    );
  }
);
export default InteractionItem2;
