import { Utils, CMD } from "@hc/engine";
import { Box, Stack, Switch } from "@mui/material";
import React, { useState } from "react";
import { IEditorState, useEditorContext } from "~/contexts/editor-context";
import {
  TOGGLE_PICKABLE,
  TOGGLE_SHADOW,
  TOGGLE_VISIABLE,
  UPDATE,
  UPDATE_NAME,
} from "~/reducers/editorReducers";
import { uiVector2Vector } from "~/utils/adapter";
import VectorPanel from "../common/vector3Panel";
import AnimationPanel from "./animationPanel";
import TitleInput from "~/components/common/titleInput";
import Title from "~/components/common/title";
import HcInput from "~/components/common/hcInput";
const EntityBasePanel: React.FC = () => {
  const { editorState, dispatch } = useEditorContext();

  const entity = editorState.entity;

  const [openKeys, setOpenKeys] = useState(new Set());

  const handleSwitchVisiable = (event: React.ChangeEvent<HTMLInputElement>) => {
    CMD.CommandWrap(() => {
      entity.Visiable = event.target.checked;
    }, "");
    dispatch({ type: TOGGLE_VISIABLE });
  };
  // const [pickable, setPickable] = React.useState(IsPickable);

  const handleChangeTF = (state: Partial<IEditorState>) => {
    dispatch({ type: UPDATE, ...state });
  };

  const handleOpenKeys = (k: string) => {
    if (openKeys.has(k)) {
      openKeys.delete(k);
    } else {
      openKeys.add(k);
    }
    setOpenKeys(new Set(openKeys));
  };

  const handleChangePick = (event: React.ChangeEvent<HTMLInputElement>) => {
    entity.IsPickable = event.target.checked;
    dispatch({ type: TOGGLE_PICKABLE });
  };
  const handleChangeShadow = (event: React.ChangeEvent<HTMLInputElement>) => {
    entity.EnableShadow = event.target.checked;
    dispatch({ type: TOGGLE_SHADOW });
  };
  return (
    <Box
      sx={{
        bgcolor: "background.paper",
        fontSize: "14px",
      }}
      component="nav"
      aria-labelledby="nested-list-subheader"
    >
      {/* <LabelRow label="" sx={{ mt: 2 }}>
          <TextField
            id="filled-hidden-label-small"
            defaultValue="0"
            size="small"
            sx={{
              paddingTop: "4px",
            }}
          />
        </LabelRow> */}
      <Stack direction="row" sx={{ mt: 2 }}>
        <HcInput
          title=""
          value={editorState.name}
          onChange={(e) =>
            dispatch({ type: UPDATE_NAME, name: e.target.value })
          }
          onBlur={(e) => {
            CMD.CommandWrap(() => {
              editorState.entity.Name = e.target.value;
            }, "");
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.5} />
        <VectorPanel
          vector={editorState.position}
          onChange={(k: string, v: string) =>
            handleChangeTF({ position: { ...editorState.position, [k]: v } })
          }
          onBlur={() => {
            CMD.CommandWrap(() => {
              entity.Position = uiVector2Vector(editorState.position);
            }, "");
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <VectorPanel
          vector={editorState.rotation}
          onBlur={() => {
            CMD.CommandWrap(() => {
              entity.Rotation = uiVector2Vector(editorState.rotation, (n) =>
                Utils.deg2rad(n)
              );
            }, "");
          }}
          onChange={(k: string, v: string) =>
            handleChangeTF({ rotation: { ...editorState.rotation, [k]: v } })
          }
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <VectorPanel
          vector={editorState.scale}
          onBlur={() => {
            CMD.CommandWrap(() => {
              entity.Scale = uiVector2Vector(editorState.scale);
            }, "");
          }}
          onChange={(k: string, v: string) =>
            handleChangeTF({ scale: { ...editorState.scale, [k]: v } })
          }
        />
      </Stack>
      {/* <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <Switch
          color="primary"
          checked={editorState.visiable}
          onChange={handleSwitchVisiable}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <Switch
          color="primary"
          checked={editorState.pickable}
          onChange={handleChangePick}
        />
      </Stack> */}
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <Switch
          color="primary"
          checked={editorState.enableShadow}
          onChange={handleChangeShadow}
        />
      </Stack>
      <div style={{ display: "none" }}>
        <AnimationPanel
          expand={() => handleOpenKeys("animate")}
          isExpand={openKeys.has("animate")}
          entity={entity}
        />
      </div>
    </Box>
  );
};
export default EntityBasePanel;
