import { IconButton, Box } from "@mui/material";
import React from "react";
import IconAli from "~/components/common/iconAli";
import CloudServices from "./cloudServices";
import { useRightStores } from "~/contexts/rightStoreContext";
import { Observer } from "mobx-react-lite";

interface CloudServicesButtonProps {}

const CloudServicesButton: React.FC<CloudServicesButtonProps> =
  React.forwardRef((props, ref: React.Ref<HTMLButtonElement>) => {
    const { cloudServicesStore } = useRightStores();

    return (
      <Observer>
        {() => (
          <>
            <IconButton
              onClick={() => (cloudServicesStore.Open = true)}
              ref={ref}
              {...props}
              sx={{ height: "40px" }}
            >
              <IconAli name="icon-3Dmoxingshuju" />
            </IconButton>
            <CloudServices />
          </>
        )}
      </Observer>
    );
  });
export default CloudServicesButton;
