import { IconButton, Box } from "@mui/material";
import React from "react";
import IconAli from "../common/iconAli";
import GlobalVariables from "./globalVariables";
import { useRightStores } from "~/contexts/rightStoreContext";
import { observer, Observer } from "mobx-react-lite";

interface GlobalVarButtonProps {}

const GlobalVarButton: React.FC<GlobalVarButtonProps> = React.forwardRef(
  (props, ref: React.Ref<HTMLButtonElement>) => {
    const { globalVarStore } = useRightStores();

    return (
      <Observer>
        {() => (
          <>
            <IconButton
              onClick={() => (globalVarStore.Open = true)}
              ref={ref}
              {...props}
              sx={{ height: "40px" }}
            >
              <IconAli name="icon-3Dhanshu" />
            </IconButton>
            {globalVarStore.Open && <GlobalVariables />}
          </>
        )}
      </Observer>
    );
  }
);
export default observer(GlobalVarButton);
