import Add from "@mui/icons-material/Add";
import { Box, IconButton, Paper, Popover } from "@mui/material";
import Button from "@mui/material/Button";
import { Observer, observer } from "mobx-react-lite";
import React, { useState } from "react";
import { InteractionStoreContext } from "~/contexts/interactionStoreContext";
import { EditorIntMode, interactionStore } from "~/stores/InteractionStore";

import ActionListItem from "./actionListItem";
import InteractionPage from "./InteractionPage";

import AspectRatioIcon from "@mui/icons-material/AspectRatio";
import AddDataPanel from "./addPanel";
import { ActionList, EventList } from "./dataList";
import Detail from "./actionDetails/detail";
import { HStack } from "../common/stack";
import { useStores } from "~/contexts/store-context";
import EmptyPanel from "../common/empty";

interface InteractionProps {}

/** */
const Interaction: React.FC<InteractionProps> = observer(() => {
  const { fileStore } = useStores();
  const [open, setOpen] = useState(false);
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(
    null
  );

  const handleClickOpen = () => {
    interactionStore.UseReal = false;
    interactionStore.mode = EditorIntMode.Modal;
    interactionStore.resetIndexs();
    interactionStore.save2VsInteractions();
    setOpen(true);
  };

  const handleStart = (event) => {
    setAnchorEl(event.currentTarget);
    interactionStore.step = 1;
  };
  const handleEnd = () => {
    setAnchorEl(null);
    interactionStore.step = 0;
  };

  const handleClose = (e, reason?: string) => {
    if (reason && reason === "backdropClick") return;
    interactionStore.mode = EditorIntMode.List;
    setOpen(false);
  };

  const handleConfirm = () => {
    interactionStore.step = 0;
    interactionStore.updateDatabase();
  };
  const handleCancel = () => {
    interactionStore.step = 0;
    interactionStore.isEditor = false;
    interactionStore.remove();
    interactionStore.updateDatabase();
  };
  const handleCloseEditor = () => {
    interactionStore.step = 0;
    if (interactionStore.isEditor) {
      interactionStore.isEditor = false;
    } else {
      interactionStore.remove();
      interactionStore.updateDatabase();
    }
  };

  const save = () => {
    fileStore.save();
  };

  if (!fileStore.currentFileId) {
    return (
      <EmptyPanel
        title={
          <Box>
            <Box></Box>
            <Box>
              <Button onClick={save}></Button>{" "}
            </Box>
          </Box>
        }
      />
    );
  }

  return (
    <InteractionStoreContext.Provider value={interactionStore}>
      <Box textAlign="right" width="100%">
        <IconButton onClick={handleClickOpen}>
          <AspectRatioIcon />
        </IconButton>
        {open && <InteractionPage onClose={handleClose} />}
      </Box>
      <Box width="100%">
        {interactionStore.RealInteractionList.map((action, index) => (
          <ActionListItem
            interaction={action}
            key={action.id}
            eventIndex={index}
          />
        ))}
      </Box>
      <Box width="100%" position="relative">
        <Button
          color="primary"
          variant="contained"
          fullWidth
          startIcon={<Add />}
          onClick={handleStart}
        >
          
        </Button>
        <Popover
          open={!!(interactionStore.step === 1)}
          anchorEl={anchorEl}
          onClose={handleEnd}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "left",
          }}
        >
          <AddDataPanel
            isList
            title=""
            dataList={EventList}
            onClose={handleEnd}
          />
        </Popover>
        <Popover
          open={
            !!(
              interactionStore.portalContainer?.isConnected &&
              interactionStore.step === 2
            )
          }
          anchorEl={interactionStore.portalContainer}
          onClose={handleEnd}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "left",
          }}
        >
          <AddDataPanel
            isList
            title=""
            dataList={ActionList}
            onClose={handleEnd}
          />
        </Popover>
        <Popover
          open={
            !!(
              interactionStore.portalContainer3?.isConnected &&
              interactionStore.step === 3
            )
          }
          anchorEl={interactionStore.portalContainer3}
          onClose={handleCloseEditor}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "left",
          }}
          PaperProps={{ sx: { overflow: "visible" } }}
        >
          <Paper sx={{ width: 300, p: 1 }}>
            <Detail />
            <HStack justifyContent="space-between" sx={{ mt: 1 }}>
              <Button color="primary" onClick={handleConfirm}>
                
              </Button>
              <Button color="error" onClick={handleCancel}>
                {interactionStore.isEditor ? "" : ""}
              </Button>
            </HStack>
          </Paper>
        </Popover>
      </Box>
    </InteractionStoreContext.Provider>
  );
});
export default Interaction;
