import React from "react";
import {
  Collapse,
  ListItemButton,
  ListItemIcon,
  Button,
  ListItemText,
  Grid,
} from "@mui/material";
import { ExpandLess, ExpandMore } from "@mui/icons-material";
import GridViewIcon from "@mui/icons-material/GridView";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { observer } from "mobx-react-lite";

type listItemProps = {
  label: string;
  type?: string;
};

interface InteractionItemProps {
  title: string;
  open: boolean;
  toggle: (event: React.MouseEvent<HTMLDivElement>) => void;
  list: listItemProps[];
  isEvent: boolean;
}

const InteractionItem: React.FC<InteractionItemProps> = ({
  title,
  open,
  toggle,
  list,
  isEvent,
}) => {
  const store = useInteractionStoresContext();

  const handleClick = (event: React.SyntheticEvent) => {
    const type = (event.target as HTMLElement).getAttribute("data-type");
    const d = list.find((l) => l.type === type);
    if (!d) return;
    if (isEvent) {
      store.changeTabIndex(1);
      store.addInteraction(d as any);
    } else {
      store.addActionItem(d);
    }
  };

  return (
    <>
      <ListItemButton onClick={toggle} data-key={title}>
        <ListItemIcon>
          <GridViewIcon />
        </ListItemIcon>
        <ListItemText primary={title} />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <Grid container spacing={1} onClick={handleClick}>
          {list.map((l, index) => (
            <Grid item xs={6} key={index} sx={{ textAlign: "center" }}>
              <Button
                fullWidth
                sx={{
                  backgroundColor: "#F4F4F4",
                  fontWeight: 400,
                  color: "#424B54",
                }}
                disabled={isEvent && store.isUsedEvent(l.type)}
                data-type={l.type}
                variant="text"
              >
                {l.label}
              </Button>
            </Grid>
          ))}
        </Grid>
      </Collapse>
    </>
  );
};
export default observer(InteractionItem);
