import { observer } from "mobx-react-lite";
import React from "react";
import {
  CommonSelectEntity,
  CommonSelectParam,
} from "~/components/common/commonInteraction";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { useRightStores } from "~/contexts/rightStoreContext";
import { App } from "~/intance";
import { InteractionConfig } from "../config";
import NoneActionDetail from "./noneAction";

interface DetailProps {}

const Detail: React.FC<DetailProps> = () => {
  const store = useInteractionStoresContext();
  const { globalVarStore } = useRightStores();

  const handleChange = (evt, opt, reason) => {
    if (reason === "selectOption") {
      if (opt.value === 0) {
        store.addActionDataItem(undefined);
      } else {
        const oid = App.intance.Database.getObjectId(opt.value) as any;
        if (oid) store.addActionDataItem(oid);
      }
    }
  };

  const handleChangeParam = (evt) => {
    if (evt.target.value) {
      store.addParamItem(evt.target.value);
    }
  };

  if (!store.CurrentActionDetail) {
    if (store.step === 3) {
      if (store.CurrentAction?.target) {
        if (store.CurrentAction?.type === "Dispatch") {
          return (
            <CommonSelectEntity
              openSelect
              value={null}
              onChange={handleChange}
              hasPage
            />
          );
        } else
          return (
            <CommonSelectEntity
              openSelect
              value={null}
              onChange={handleChange}
            />
          );
      }
      if (store.CurrentAction?.param) {
        return (
          <CommonSelectParam
            variables={globalVarStore.variables}
            onChange={handleChangeParam}
          />
        );
      }
    }
    return <NoneActionDetail />;
  }

  const action = store.CurrentAction;

  if (InteractionConfig[action.type]?.detail)
    return InteractionConfig[action.type].detail;

  return <div></div>;
};
export default observer(Detail);
