import { CMD, Utils } from "@hc/engine";
import { Box, Stack } from "@mui/material";
import React, { useState } from "react";
import { IEditorState, useEditorContext } from "~/contexts/editor-context";
import {
  TOGGLE_VISIABLE,
  UPDATE,
  UPDATE_NAME,
} from "~/reducers/editorReducers";
import { uiVector2Vector } from "~/utils/adapter";
import LabelRow from "../common/labelRow";
import Vector3Panel from "../common/vector3Panel";
import TitleInput from "~/components/common/titleInput";
import Title from "~/components/common/title";
import { debounceCallback } from "~/utils/time";

interface GuiBasePanelProps {}

const GuiBasePanel: React.FC<GuiBasePanelProps> = () => {
  const { editorState, dispatch } = useEditorContext();

  const gui = editorState.gui;

  const handleChangeTF = (state: Partial<IEditorState>) => {
    dispatch({ type: UPDATE, ...state });
  };

  const handleChange = (evt) => {
    dispatch({
      type: UPDATE,
      other: { ...editorState.other, [evt.target.name]: evt.target.value },
    });

    if (evt.target.value)
      debounceCallback(() => {
        CMD.CommandWrap(() => {
          gui[evt.target.name] = evt.target.value;
        }, "");
      });
  };

  return (
    <Box
      sx={{
        width: "100%",
        maxWidth: 360,
        bgcolor: "background.paper",
        fontSize: "14px",
      }}
      component="nav"
      aria-labelledby="nested-list-subheader"
    >
      {/* <LabelRow label="" sx={{ mt: 2 }}>
          <TextField
            id="filled-hidden-label-small"
            defaultValue="0"
            size="small"
            sx={{
              paddingTop: "4px",
            }}
          />
        </LabelRow> */}
      <Stack direction="row" sx={{ mt: 2 }}>
        <TitleInput
          title=""
          value={editorState.name}
          onChange={(e) =>
            dispatch({ type: UPDATE_NAME, name: e.target.value })
          }
          onBlur={(e) => {
            CMD.CommandWrap(() => {
              gui.Name = e.target.value;
            }, "");
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <TitleInput
          name="Width"
          title=""
          value={editorState.other.Width}
          onChange={handleChange}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <TitleInput
          name="Height"
          title=""
          value={editorState.other.Height}
          onChange={handleChange}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <Vector3Panel
          vector={editorState.position}
          onChange={(k: string, v: string) =>
            handleChangeTF({ position: { ...editorState.position, [k]: v } })
          }
          onBlur={() => {
            CMD.CommandWrap(() => {
              gui.Position = editorState.position;
            }, "");
          }}
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title={""} mt={0.7} />
        <Vector3Panel
          vector={editorState.rotation}
          onBlur={() => {
            CMD.CommandWrap(() => {
              gui.Rotation = Utils.deg2rad(parseFloat(editorState.rotation.x));
            }, "");
          }}
          onChange={(k: string, v: string) =>
            handleChangeTF({ rotation: { ...editorState.rotation, [k]: v } })
          }
        />
      </Stack>
      <Stack direction="row" sx={{ mt: 2 }}>
        <Title title="" mt={0.7} />
        <Vector3Panel
          vector={editorState.scale}
          onBlur={() => {
            CMD.CommandWrap(() => {
              gui.Scale = uiVector2Vector(editorState.scale);
            }, "");
          }}
          onChange={(k: string, v: string) =>
            handleChangeTF({ scale: { ...editorState.scale, [k]: v } })
          }
        />
      </Stack>
    </Box>
  );
};
export default GuiBasePanel;
