import {
  Box,
  List,
  Stack,
  Typography,
  Paper,
  InputBase,
  IconButton,
} from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import SearchIcon from "@mui/icons-material/Search";

import InteractionItem from "./InteractionItem";
import InteractionItem2 from "./InteractionItem2";

interface AddEventPanelProps {
  dataList: any[];
  title: string;
  isList?: boolean;
  onClose?: () => void;
}
let unMount = false;
let timer;

const AddDataPanel: React.FC<AddEventPanelProps> = ({
  dataList,
  title,
  isList,
  onClose,
}) => {
  const [query, setQuery] = useState("");
  const containerRef = useRef<HTMLElement>(null);
  const [openKeys, setOpenKeys] = useState(
    new Set(dataList.map((l) => l.label))
  );

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    const k = event.currentTarget.getAttribute("data-key");
    if (openKeys.has(k)) openKeys.delete(k);
    else openKeys.add(k);
    setOpenKeys(new Set(openKeys));
  };

  const handleBlur = (evt: React.FocusEvent) => {
    let relaElement = evt.relatedTarget as HTMLElement;
    if (!relaElement) return;
    const parent = containerRef.current;

    timer = setTimeout(() => {
      if (unMount) return;
      if (!parent?.parentNode) return;
      while (relaElement) {
        if (relaElement.parentElement === parent) {
          return;
        }
        relaElement = relaElement.parentElement;
      }
      onClose?.();
    }, 200);
  };

  useEffect(() => {
    return () => {
      timer && clearTimeout(timer);
      unMount = true;
    };
  });

  if (isList) {
    return (
      <Box
        sx={{
          width: isList ? 250 : "100%",
          bgcolor: "background.paper",
          p: 1,
        }}
        ref={containerRef}
        component={Paper}
      >
        <Stack flexDirection="row" sx={{ height: "5%" }}>
          <Paper
            sx={{
              display: "flex",
              alignItems: "center",
              width: "100%",
            }}
          >
            <InputBase
              value={query}
              sx={{ flex: 1 }}
              placeholder=""
              autoFocus
              onBlur={handleBlur}
            />
            <IconButton>
              <SearchIcon fontSize="small" />
            </IconButton>
          </Paper>
        </Stack>
        <List
          sx={{ overflowY: "auto", maxHeight: 400 }}
          component="nav"
          aria-labelledby="nested-list-subheader"
        >
          {dataList.map((data) => (
            <InteractionItem2
              key={data.label}
              data={data}
              isEvent={title.includes("")}
            />
          ))}
        </List>
      </Box>
    );
  }

  return (
    <Box
      sx={{
        width: "100%",
        maxWidth: 360,
        bgcolor: "background.paper",
        overflowY: "auto",
      }}
    >
      <Typography> {title}</Typography>
      <List component="nav" aria-labelledby="nested-list-subheader">
        {dataList.map((data) => (
          <InteractionItem
            key={data.label}
            title={data.label}
            open={openKeys.has(data.label)}
            toggle={handleClick}
            list={data.list}
            isEvent={title.includes("")}
          />
        ))}
      </List>
    </Box>
  );
};
export default AddDataPanel;
