import { History } from "@hc/engine";
import { Box } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useEditorContext } from "~/contexts/editor-context";
import config, { IProperty } from "./config";
import ReadProperty from "./readProperty";
import ComponentDetail from "./componentDetail";
import { Component, end } from "@hc/engine";
import { HC2DGui } from "~/extension/Hc2DGui";
import { HC2DDom } from "~/extension/HC2DDom";
import { App } from "~/intance";
import { UPDATE } from "~/reducers/editorReducers";

const CMD_NAMES = ["", "DOM", "DOM", ""];

const Property: React.FC = () => {
  const { editorState, dispatch } = useEditorContext();
  const [props, setProps] = useState<IProperty[]>([]);

  const handleReUn = (hm: History.CommandHistoryRecord) => {
    if (CMD_NAMES.some((cmd) => hm.commandName.includes(cmd))) {
      dispatch({ type: UPDATE });
    }
  };

  useEffect(() => {
    const clears = [
      end(
        App.intance.Database.HistoryManager,
        App.intance.Database.HistoryManager.redoEvent,
        (e) => {
          handleReUn(e);
        }
      ),
      end(
        App.intance.Database.HistoryManager,
        App.intance.Database.HistoryManager.undoEvent,
        (e) => {
          handleReUn(e);
        }
      ),
    ];
    return () => {
      clears.forEach((c) => c());
    };
  }, []);

  useEffect(() => {
    let props = [];
    if (editorState.gui) {
      props = editorState.gui.DataList.map((d) => ({
        key: d.key,
        value: d.value,
        type: "number",
        title: d.label,
        unit: d.unit,
      }));

      if (editorState.gui instanceof HC2DGui) {
        const databinds = editorState.gui.DataBindings;
        for (const d of databinds) {
          if (d.description === "basic_hide") continue;
          props.push({
            key: d.attr,
            value: editorState.gui.Attr[d.attr] ?? d.defaultValue,
            type: d.valueType,
            title: d.name,
            unit: "",
          });
        }
      }
    }

    if (editorState.dom) {
      if (editorState.dom instanceof HC2DDom) {
        const databinds = editorState.dom.DataBindings;
        for (const d of databinds) {
          if (d.description === "basic_hide") continue;
          props.push({
            key: d.attr,
            value: editorState.dom.Attr[d.attr] ?? d.defaultValue,
            type: d.valueType,
            title: d.name,
            unit: "",
          });
        }
      }
    }

    if (editorState.entity) {
      if (config[editorState.type]) {
        props = [...config[editorState.type]];
        for (const prop of props) {
          prop.value = editorState.entity[prop.key];
        }
      }
    }

    setProps(props);
  }, [editorState]);

  if (editorState.entity?.IsComponent) {
    return (
      <Box>
        <ComponentDetail entity={editorState.entity as Component} />
      </Box>
    );
  }

  if (editorState.gui) {
    return (
      <Box>
        <ReadProperty entity={editorState.gui} properties={props} />
      </Box>
    );
  }

  if (editorState.dom) {
    return (
      <Box>
        <ReadProperty entity={editorState.dom} properties={props} />
      </Box>
    );
  }

  return (
    <Box>
      <ReadProperty entity={editorState.entity} properties={props} />
    </Box>
  );
};
export default Property;
