import React, { useState, useEffect, useMemo } from "react";
import { TextField, ListItemText, MenuItem, Stack, Box } from "@mui/material";
import { useStores } from "~/contexts/store-context";
import { when, runInAction, reaction } from "mobx";
import { App } from "~/intance";
import Title from "~/components/common/title";
import { SkyboxMode } from "@hc/engine";
import DefaultSceneConfig from "./defaultConfig";
import SunSceneConfig from "./SunConfig";
import { Observer } from "mobx-react-lite";
import SkyboxConfig from "./SkyboxConfig";

interface BgTypeProps {}

const bgTypes = [
  { label: "", value: SkyboxMode.None },
  { label: "", value: SkyboxMode.Image },
  { label: "", value: SkyboxMode.Sun },
  // { label: "", value: SkyboxMode.Env },
];
const BgType: React.FC<BgTypeProps> = () => {
  const [type, setType] = useState(SkyboxMode.None);
  const { fileStore, rootStore } = useStores();
  const handleChangeBgType = (e) => {
    rootStore.BgType = e.target.value;
    App.intance.Viewer.sceneControl.Mode = e.target.value;
  };
  const SceneConfig = useMemo(() => {
    let bgType = rootStore.BgType;
    if (bgType === SkyboxMode.None) {
      return <DefaultSceneConfig />;
    } else if (bgType === SkyboxMode.Image) {
      return;
    } else if (bgType === SkyboxMode.Sun) {
      return <SunSceneConfig />;
    }
    return <></>;
  }, [type]);

  useEffect(() => {
    reaction(
      () => rootStore.BgType,
      () => {
        setType(rootStore.BgType);
      }
    );
  }, []);
  useEffect(() => {
    when(
      () => !!App.intance,
      () => {
        const app = App.intance;
        rootStore.BgType = app.Viewer.sceneControl.Mode;
      }
    );
  }, [fileStore.currentFileName]);
  return (
    <Observer>
      {() => (
        <>
          <Box>
            <Stack direction="row" sx={{ mt: 2 }}>
              <Title title="" />
              <Box sx={{ position: "relative", width: 165 }}>
                <TextField
                  id="outlined-select-currency"
                  select
                  value={rootStore.BgType}
                  onChange={handleChangeBgType}
                  size="small"
                  placeholder=""
                  // eslint-disable-next-line prettier/prettier
                  sx={{ width: 165 }}
                  InputProps={{
                    sx: {
                      height: 24,
                      borderRadius: 1,
                      background: "#F4F4F4",
                      border: 0,
                      fontSize: 14,
                    },
                  }}
                  inputProps={{
                    sx: {
                      pt: 1,
                      height: 24,
                      fontSize: 14,
                    },
                  }}
                >
                  {bgTypes.map((option) => (
                    <MenuItem
                      key={option.value}
                      value={option.value}
                      sx={{ fontSize: 14 }}
                    >
                      {option.label}
                    </MenuItem>
                  ))}
                </TextField>
              </Box>
            </Stack>
          </Box>
          {SceneConfig}
        </>
      )}
    </Observer>
  );
};
export default BgType;
