import TreeItem, {
  treeItemClasses,
  TreeItemContentProps,
  TreeItemProps,
  useTreeItem,
} from "@mui/lab/TreeItem";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import { SvgIconProps } from "@mui/material/SvgIcon";
import Typography from "@mui/material/Typography";
import { Observer } from "mobx-react-lite";
import * as React from "react";

declare module "react" {
  interface CSSProperties {
    "--tree-view-color"?: string;
    "--tree-view-bg-color"?: string;
  }
}

type StyledTreeItemProps = TreeItemProps & {
  bgColor?: string;
  color?: string;
  labelIcon?: React.ElementType<SvgIconProps>;
  endElement?: string | JSX.Element;
  labelText: string;
  desc?: React.ReactNode;
};

const StyledTreeItemRoot = styled(TreeItem)(({ theme }) => ({
  color: theme.palette.text.secondary,
  [`& .${treeItemClasses.content}`]: {
    color: theme.palette.text.secondary,
    borderTopRightRadius: theme.spacing(2),
    borderBottomRightRadius: theme.spacing(2),
    paddingRight: theme.spacing(1),
    fontWeight: theme.typography.fontWeightMedium,
    "&.Mui-expanded": {
      fontWeight: theme.typography.fontWeightRegular,
    },
    "&:hover": {
      backgroundColor: theme.palette.action.hover,
    },
    "&.Mui-focused, &.Mui-selected, &.Mui-selected.Mui-focused": {
      backgroundColor: `var(--tree-view-bg-color, ${theme.palette.action.selected})`,
      color: "var(--tree-view-color)",
    },
    [`& .${treeItemClasses.label}`]: {
      fontWeight: "inherit",
      color: "inherit",
    },
  },
  //   [`& .${treeItemClasses.group}`]: {
  //     marginLeft: 0,
  //     [`& .${treeItemClasses.content}`]: {
  //       paddingLeft: theme.spacing(4),
  //     },
  //   },
}));

const CustomContent = React.forwardRef(function CustomContent(
  props: TreeItemContentProps & { descption?: React.ReactNode },
  ref: React.Ref<HTMLDivElement>
) {
  const {
    classes,
    className,
    label,
    nodeId,
    descption,
    icon: iconProp,
    expansionIcon,
    displayIcon,
    onClick,
    onMouseDown,
  } = props;

  const {
    disabled,
    expanded,
    selected,
    focused,
    handleExpansion,
    handleSelection,
    preventSelection,
  } = useTreeItem(nodeId);

  const icon = iconProp || expansionIcon || displayIcon;

  const handleMouseDown = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    preventSelection(event);
    onMouseDown?.(event);
  };

  const handleExpansionClick = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    handleExpansion(event);
  };

  const handleSelectionClick = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    handleSelection(event);
    onClick?.(event);
  };

  const classNames = React.useMemo(() => {
    const classNamesSet = new Set(className.split(" "));
    classNamesSet.add(classes.root);
    if (expanded) {
      classNamesSet.add(classes.expanded);
    }
    if (selected) {
      classNamesSet.add(classes.selected);
    }
    if (focused) {
      classNamesSet.add(classes.focused);
    }
    if (disabled) {
      classNamesSet.add(classes.disabled);
    }
    return Array.from(classNamesSet).join(" ");
  }, [disabled, focused, selected, expanded]);

  return (
    <div className={classNames} onMouseDown={handleMouseDown} ref={ref}>
      <div onClick={handleExpansionClick} className={classes.iconContainer}>
        {icon}
      </div>
      <Typography
        onClick={handleSelectionClick}
        component="div"
        className={classes.label}
      >
        {label}
      </Typography>
    </div>
  );
});

export const StyledTreeItem = React.forwardRef(
  (props: StyledTreeItemProps, ref) => {
    const {
      bgColor,
      color,
      labelIcon: LabelIcon,
      endElement,
      labelText,
      desc,
      ...other
    } = props;

    return (
      <Observer>
        {() => (
          <StyledTreeItemRoot
            ContentComponent={CustomContent}
            ref={ref}
            label={
              <>
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    flexWrap: "wrap",
                  }}
                >
                  {LabelIcon && (
                    <Box component={LabelIcon} color="inherit" sx={{ mr: 1 }} />
                  )}
                  <Typography
                    variant="body2"
                    sx={{ fontWeight: "inherit", flexGrow: 1 }}
                  >
                    {labelText}
                  </Typography>
                  <Typography variant="caption" color="inherit">
                    {endElement}
                  </Typography>
                </Box>
                {desc && (
                  <Typography
                    variant="subtitle2"
                    color="inherit"
                    sx={{ ml: 3 }}
                  >
                    {desc}
                  </Typography>
                )}
              </>
            }
            style={{
              "--tree-view-color": color,
              "--tree-view-bg-color": bgColor,
            }}
            {...other}
          />
        )}
      </Observer>
    );
  }
);
