import React, { useEffect, useState } from "react";
import { useEditorContext } from "~/contexts/editor-context";
import DomBasePanel from "./domBasePanel";

import EntityBasePanel from "./entityBasePanel";
import GuiBasePanel from "./guiBasePanel";

const BasePanel: React.FC = () => {
  const { editorState } = useEditorContext();

  if (editorState.entity) {
    return <EntityBasePanel />;
  }

  if (editorState.gui) {
    return <GuiBasePanel />;
  }
  if (editorState.dom) {
    return <DomBasePanel />;
  }

  return <div></div>;
};
export default BasePanel;
