import {
  BaseDom,
  BaseGui,
  BaseObject,
  CMD,
  end,
  Entity,
  History,
  Interaction,
  Light,
  ParticleSystem,
  Particle,
  Utils,
  ParticleSystemGroup,
  GroupRecord,
} from "@hc/engine";
import DeleteIcon from "@mui/icons-material/Delete";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import { TreeItem } from "@mui/lab";
import { treeItemClasses } from "@mui/lab/TreeItem";
import {
  Box,
  Collapse,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Typography,
} from "@mui/material";
import { styled } from "@mui/system";
import { when } from "mobx";
import React, { useEffect, useRef, useState } from "react";
import { App } from "~/intance";
import SelectAllIcon from "@mui/icons-material/SelectAll";
import DeselectIcon from "@mui/icons-material/Deselect";
import Resizable from "react-resizable-box";
import { HStack } from "../common/stack";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import StopIcon from "@mui/icons-material/Stop";
import { useEditorContext } from "~/contexts/editor-context";
import { SELECT_ENTITY, SELECT_GUI } from "~/reducers/editorReducers";
import AcUnitIcon from "@mui/icons-material/AcUnit";
import AirportShuttleIcon from "@mui/icons-material/AirportShuttle";
import LightIcon from "@mui/icons-material/Light";
import AssessmentIcon from "@mui/icons-material/Assessment";
import AssignmentIcon from "@mui/icons-material/Assignment";
import GroupWorkIcon from "@mui/icons-material/GroupWork";

const Resizable2 = Resizable as any;
interface SceneCollectionProps {}

const NEED_UPDATE_CMD_NAMES = ["", "DELETE", "", ""];

const CustomTreeItem = styled(TreeItem)(({ theme }) => ({
  [`& .${treeItemClasses.content}`]: {
    "&.Mui-focused, &.Mui-selected, &.Mui-selected.Mui-focused": {
      backgroundColor: `#a3d1d5`,
    },
  },
  [`& .${treeItemClasses.group}`]: {
    maxHeight: 200,
    overflowY: "auto",
  },
}));

const TreeLabel = ({ object }) => {
  const [visiable, setVisiable] = useState(false);
  const handleDelete = () => {
    CMD.CommandWrap(() => {
      object.object.erase();
    }, "" + object.object.Name);
  };

  const toggleVisiable = () => {
    setVisiable(!visiable);
    if (object.object) {
      CMD.CommandWrap(() => {
        object.object.Visiable = !object.object.Visiable;
      }, "");
    }
  };
  useEffect(() => {
    setVisiable(object.object?.Visiable);
  }, [object]);

  return (
    <HStack alignItems="center">
      <Typography sx={{ flex: 1 }}>{object.label}</Typography>
      <HStack sx={{ width: "30%" }}>
        {object.canVisiable && (
          <IconButton size="small" onClick={toggleVisiable}>
            {visiable ? <VisibilityIcon /> : <VisibilityOffIcon />}
          </IconButton>
        )}
        <IconButton size="small" onClick={handleDelete} color="error">
          <DeleteIcon />
        </IconButton>
      </HStack>
    </HStack>
  );
};

let isClickTree = false;
let isInit = true;

let selectId: number;
let sceneList: any[] = [];

const SceneCollection: React.FC<SceneCollectionProps> = (props) => {
  const [open, setOpen] = useState(true);
  const [tree, setTree] = useState([]);
  const [selectIds, setSelectIds] = useState<number[]>([]);
  const ulRef = useRef<HTMLElement>(null);
  const [hiddenIds, setHiddenIds] = useState<Set<number>>(new Set());
  const [unSelectIds, setUnselectIds] = useState<Set<number>>(new Set());
  const [stopParticles, setStopParticles] = useState<Set<number>>(new Set());
  const { dispatch } = useEditorContext();

  const handleClick = () => {
    setOpen(!open);
  };

  const select = (data: { object: BaseObject; id: number }) => {
    isClickTree = true;

    setSelectIds([data.id]);

    const obj = data.object;

    if (obj) {
      if (obj instanceof Entity) {
        if (obj.IsPickable)
          App.intance.Editor.selectControl.SelectEntitys = [obj];
        else {
          isClickTree = false;
          dispatch({
            type: SELECT_ENTITY,
            entity: obj,
          });
        }
      } else if (obj instanceof BaseDom) {
        App.intance.Editor.selectControl.SelectDOM = obj;
      } else if (obj instanceof BaseGui) {
        if (obj.IsPickable) App.intance.Editor.selectControl.SelectGui = obj;
        else {
          dispatch({
            type: SELECT_GUI,
            gui: obj,
          });
        }
      } else if (obj instanceof Particle) {
        App.intance.Editor.selectControl.SelectParticle = obj;
      }
    }
  };

  const dbClick = (obj) => {
    if (obj) {
      if (obj instanceof Entity) {
        App.intance.Viewer.cameraManager.zoomToEntity(obj);
      }
    }
  };

  const handleToggleVisiable = (object: BaseObject, evt: React.MouseEvent) => {
    evt.stopPropagation();
    if (object) {
      if (hiddenIds.has(object.Id)) {
        hiddenIds.delete(object.Id);
      } else {
        hiddenIds.add(object.Id);
      }
      setHiddenIds(new Set(hiddenIds));
      CMD.CommandWrap(() => {
        object["Visiable"] = !object["Visiable"];
      }, "");
    }
  };

  const handleToggleSelect = (object: BaseObject, evt: React.MouseEvent) => {
    evt.stopPropagation();
    if (unSelectIds.has(object.Id)) {
      unSelectIds.delete(object.Id);
    } else {
      unSelectIds.add(object.Id);
    }

    setUnselectIds(new Set(unSelectIds));

    CMD.CommandWrap(() => {
      object["IsPickable"] = !object["IsPickable"];
    }, "");
  };
  const handleToggleStop = (object: ParticleSystem) => {
    if (stopParticles.has(object.Id)) {
      stopParticles.delete(object.Id);
      object.start();
    } else {
      stopParticles.add(object.Id);
      object.stop();
    }

    setStopParticles(stopParticles);
  };

  const handleDeleteObject = (object: BaseObject) => {
    CMD.CommandWrap(() => {
      object.erase();
    }, "" + object.Name);
  };

  const init = (objects = App.intance.Database.AllObjects) => {
    const options = [];
    const hiddenIds = new Set<number>();
    const unSelectIds = new Set<number>();
    const stopIds = new Set<number>();
    for (const object of objects) {
      if (object.IsErase) continue;
      if (object instanceof Interaction.Interaction) continue;
      if (object instanceof Light) continue;
      if (object instanceof Entity && object.GroupId) continue;

      let canVisiable = true;
      let canPick = true;
      let canStop = false;
      let icon;
      let children;

      if (
        object instanceof ParticleSystem ||
        object instanceof ParticleSystemGroup
      ) {
        canVisiable = false;
        canPick = false;
        canStop = true;
        icon = AcUnitIcon;
        stopIds.add(object.Id);
      }

      if (object instanceof Entity) {
        icon = AirportShuttleIcon;
        if (object.isLight) {
          icon = LightIcon;
        }
      }

      if (object instanceof BaseGui) {
        icon = AssessmentIcon;
      }
      if (object instanceof BaseDom) {
        icon = AssignmentIcon;
      }

      if (object instanceof GroupRecord) {
        icon = GroupWorkIcon;
        children = init(object.entitys.map((i) => i.Object));
      }

      if ("Visiable" in object) {
        if (!object["Visiable"]) {
          hiddenIds.add(object.Id);
        }
      } else {
        canVisiable = false;
      }

      if ("IsPickable" in object) {
        if (!object["IsPickable"]) {
          unSelectIds.add(object.Id);
        }
      } else {
        canPick = false;
      }

      options.push({
        label: object.Name,
        id: object.Id,
        object,
        canVisiable,
        canPick,
        canStop,
        icon,
        children,
      });
    }
    setHiddenIds(hiddenIds);
    setUnselectIds(unSelectIds);
    setTree(options);
    setStopParticles(stopIds);
    sceneList = options;
  };

  const handleDelete = (evt: React.KeyboardEvent) => {
    evt.stopPropagation();
    if (evt.code === Utils.KeyCode.Delete)
      CMD.commandManager.ExecCommand("DELETE");
  };

  const updateScroll = () => {
    if (ulRef.current && selectId) {
      const index = sceneList.findIndex((t) => t.id === selectId);

      if (index >= 0) {
        const scrollTop = 48 * index;
        ulRef.current.scrollTop = scrollTop;
      }
    }
  };

  const handleRedoUndo = (e: History.CommandHistoryRecord) => {
    App.intance.Editor.selectControl.clearSelect(true);
    if (NEED_UPDATE_CMD_NAMES.some((cmd) => e.commandName.includes(cmd))) {
      init();
    }
  };

  useEffect(() => {
    let injects: (() => void)[] = [];

    when(
      () => !!App.intance,
      () => {
        init();
        //TODO:
        injects.push(
          end(
            App.intance.Database.ModelSpace,
            App.intance.Database.ModelSpace.appendEvent,
            (e) => {
              if (isInit) init();
            }
          ),
          end(
            App.intance.Database.LightSpace,
            App.intance.Database.LightSpace.appendEvent,
            (e) => {
              if (isInit) init();
            }
          ),
          end(
            App.intance.Database.GuiSpace,
            App.intance.Database.GuiSpace.appendEvent,
            (e) => {
              if (isInit) init();
            }
          ),
          end(
            App.intance.Database.DomSpace,
            App.intance.Database.DomSpace.appendEvent,
            (e) => {
              if (isInit) init();
            }
          ),
          end(
            App.intance.Database.ParticleSystemSpace,
            App.intance.Database.ParticleSystemSpace.appendEvent,
            (e) => {
              if (isInit) init();
            }
          ),
          end(
            App.intance.Database.GroupTable,
            App.intance.Database.GroupTable.appendEvent,
            (e) => {
              if (isInit) init();
            }
          ),
          App.intance.on("updateName", () => {
            if (isInit) init();
          }),
          App.intance.on("updateVisiable", () => {
            if (isInit) init();
          }),
          App.intance.on("remove", () => {
            console.log("remove");
            if (isInit) init();
          }),
          App.intance.on("startLoad", () => {
            isInit = false;
          }),
          App.intance.on("loadFile", () => {
            init();
            isInit = true;
          }),
          App.intance.Editor.selectControl.on((event) => {
            if (event.type === "select") {
              if (isClickTree) {
                isClickTree = false;
                return;
              }
              const en = event.entitys?.[0];
              selectId = undefined;
              if (event.entitys) {
                const ids = event.entitys.map((e) => e.Id);
                setSelectIds(ids);
                selectId = ids[0];
              } else {
                setSelectIds([]);
              }
              updateScroll();
            }
          }),
          end(
            App.intance.Database.HistoryManager,
            App.intance.Database.HistoryManager.redoEvent,
            (e) => {
              handleRedoUndo(e);
            }
          ),
          end(
            App.intance.Database.HistoryManager,
            App.intance.Database.HistoryManager.undoEvent,
            (e) => {
              handleRedoUndo(e);
            }
          )
        );
      }
    );
    return () => {
      injects.forEach((f) => f());
    };
  }, []);

  return (
    <Box
      sx={{
        maxHeight: "100%",
        minHeight: 55,
        borderBottom: 2,
        borderColor: "divider",
      }}
    >
      <Resizable2
        height={225}
        minHeight={150}
        maxHeight={840}
        enable={{
          top: true,
          right: false,
          bottom: false,
          left: false,
          topRight: false,
          bottomRight: false,
          bottomLeft: false,
          topLeft: false,
        }}
      >
        <ListItemButton onClick={handleClick} sx={{ bgcolor: "#ccc" }}>
          <ListItemText primary="" />
          {/* {open ? <ExpandLess /> : <ExpandMore />} */}
        </ListItemButton>
        {/* <Collapse in={true} timeout="auto" unmountOnExit> */}
        <Box
          sx={{
            height: "calc(100% - 50px)",
            overflowY: "auto",
          }}
          ref={ulRef}
        >
          <List>
            {tree.map((t) => (
              <ListItem
                key={t.id}
                onClick={() => select(t)}
                onDoubleClick={() => dbClick(t.object)}
                sx={{
                  cursor: "pointer",
                  "&:hover": { backgroundColor: "#ccc" },
                  backgroundColor: selectIds.includes(t.id)
                    ? "#a3d1d5"
                    : undefined,
                }}
                secondaryAction={
                  <>
                    {t.canVisiable && (
                      <IconButton
                        edge="end"
                        aria-label="delete"
                        onClick={(evt) => handleToggleVisiable(t.object, evt)}
                        title="/"
                      >
                        {hiddenIds.has(t.id) ? (
                          <VisibilityOffIcon />
                        ) : (
                          <VisibilityIcon />
                        )}
                      </IconButton>
                    )}
                    {t.canPick && (
                      <IconButton
                        edge="end"
                        aria-label="delete"
                        onClick={(evt) => handleToggleSelect(t.object, evt)}
                        title=""
                      >
                        {unSelectIds.has(t.id) ? (
                          <DeselectIcon />
                        ) : (
                          <SelectAllIcon />
                        )}
                      </IconButton>
                    )}
                    {t.canStop && (
                      <IconButton
                        edge="end"
                        aria-label="stop"
                        onClick={() => handleToggleStop(t.object)}
                        title=""
                      >
                        {stopParticles.has(t.id) ? (
                          <PlayArrowIcon />
                        ) : (
                          <StopIcon />
                        )}
                      </IconButton>
                    )}

                    {/* <IconButton
                    edge="end"
                    aria-label="delete"
                    onClick={() => handleDeleteObject(t.object)}
                  >
                    <DeleteIcon />
                  </IconButton> */}
                  </>
                }
              >
                {t.icon && (
                  <ListItemIcon sx={{ minWidth: 30 }}>
                    <t.icon />
                  </ListItemIcon>
                )}

                <ListItemText primary={t.label} />
              </ListItem>
            ))}
          </List>
        </Box>
      </Resizable2>
    </Box>
  );
};
export default SceneCollection;
