import md5 from "md5-node";
import { HistronHost } from "~/apis/index";
const wsfix = location.protocol === "https:" ? "wss" : "ws";
function getURL(realm) {
  if (typeof realm === "string") {
    let index = realm.indexOf("://");
    if (index > -1) {
      // let endIndex = realm.length - 1;
      return realm.slice(index + 3);
    }
  } else {
    return HistronHost;
  }
}
export const encryptStgy = {
  histron: function (msg) {
    let result,
      timestamp,
      clientid,
      username,
      sign,
      { deviceKey, deviceSecret, prodId, realm } = msg;

    let url = getURL(realm);
    let baseUrl = `${wsfix}://${url}/device/ws/dev/send/`;
    clientid = `dev:${prodId}:${deviceKey}`;
    timestamp = new Date().getTime();
    username = `md5:${timestamp}`;

    sign = md5(`${prodId}${deviceKey}${deviceSecret}${timestamp}`);
    result = `${baseUrl}${clientid}/${username}/${sign}`;
    return result;
  },
  ali: function (msg) {
    let result, baseUrl, productKey, selectId, deviceName;
    baseUrl = `${wsfix}://${process.env.VUE_APP_HOST}/api/b/ws/aliyun`;
    deviceName = msg.deviceKey;
    productKey = msg.prodId;
    selectId = msg.selectId;
    result = `${baseUrl}/${selectId}/${productKey}/${deviceName}`;
    return result;
  },
};
