import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  Paper,
  Typography,
} from "@mui/material";
import React from "react";
import DialogHeader from "../common/dialogHeader";
import { HStack } from "../common/stack";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import { useRightStores } from "~/contexts/rightStoreContext";
import { observer } from "mobx-react-lite";
import EditorCell from "../editorModal/editorCell";

interface GlobalVariablesProps {}

const rows = [
  {
    key: "haha",
    value: "123",
    desc: "test",
  },
];

const GlobalVariables: React.FC<GlobalVariablesProps> = () => {
  const { globalVarStore } = useRightStores();

  const handleClose = () => {
    globalVarStore.cancel();
  };

  const handleConfirm = () => {
    globalVarStore.confirm();
  };

  return (
    <Dialog
      open={globalVarStore.Open}
      maxWidth="md"
      PaperProps={{ sx: { height: "522px" } }}
    >
      <DialogHeader onClose={handleClose} title="" />
      <DialogContent>
        <HStack>
          <Button onClick={() => globalVarStore.add()}></Button>
          <Button onClick={() => globalVarStore.up()}></Button>
          <Button onClick={() => globalVarStore.down()}></Button>
          <Button onClick={() => globalVarStore.delete()}></Button>
        </HStack>
        <TableContainer component={Paper} sx={{ height: "calc(100% - 40px)" }}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table" stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
                <TableCell align="center"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {globalVarStore.variables.map((vb, index) => (
                <TableRow
                  onClick={() => (globalVarStore.CurrentIndex = index)}
                  key={vb.key}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  selected={globalVarStore.CurrentIndex === index}
                >
                  <EditorCell
                    align="center"
                    defaultValue={vb.key}
                    onBlur={(evt) => {
                      if (evt.target.value) {
                        globalVarStore.update(vb.key, {
                          key: evt.target.value,
                        });
                      }
                    }}
                  />
                  <EditorCell
                    align="center"
                    defaultValue={
                      typeof vb.value === "boolean"
                        ? String(vb.value)
                        : vb.value
                    }
                    onBlur={(evt) => {
                      if (evt.target.value) {
                        globalVarStore.update(vb.key, {
                          value: evt.target.value,
                        });
                      }
                    }}
                  />
                  <EditorCell
                    align="center"
                    defaultValue={vb.desc}
                    onBlur={(evt) => {
                      if (evt.target.value) {
                        globalVarStore.update(vb.key, {
                          desc: evt.target.value,
                        });
                      }
                    }}
                  />
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </DialogContent>
      <DialogActions sx={{ justifyContent: "space-between" }}>
        <Box>
          <Typography variant="subtitle2"></Typography>
          <Typography variant="subtitle2">
            
          </Typography>
        </Box>
        <HStack>
          <Button
            onClick={handleConfirm}
            autoFocus
            variant="contained"
            sx={{ mr: 2 }}
          >
            
          </Button>
          <Button
            autoFocus
            onClick={handleClose}
            variant="contained"
            color="error"
          >
            
          </Button>
        </HStack>
      </DialogActions>
    </Dialog>
  );
};
export default observer(GlobalVariables);
