import { BaseDom, BaseGui, CMD, Entity } from "@hc/engine";
import { Box, Drawer } from "@mui/material";
import React, { useEffect, useState } from "react";
import { HC2DDom } from "~/extension/HC2DDom";
import { App } from "~/intance";

import DataBinding from "../DataBinding";
import { getTargetDataBind } from "../DataBinding/utils";
import { ValueItemContainer } from "./common";
import { IProperty } from "./config";
import ValueContent from "./valueContent";

const ValueList: React.FC<{
  props: IProperty[];
  entity: Entity | BaseGui | BaseDom;
  onClick: (evt) => void;
  onUnAttach: (evt) => void;
  onUpdate: () => void;
}> = ({ props, entity, onClick, onUnAttach, onUpdate }) => {
  const handleConfirm = (prop) => {
    return (e) => {
      CMD.CommandWrap(() => {
        if (entity instanceof BaseGui || entity instanceof HC2DDom) {
          entity.setProp(prop.key, e.target.value);
        } else entity[prop.key] = parseFloat(e.target.value);
      }, "");
    };
  };

  const handleChange = (prop) => {
    return (e) => {
      prop.value = e.target.value;
      onUpdate();
    };
  };
  return (
    <>
      {props.map((prop) => (
        <ValueItemContainer
          key={prop.key}
          label={prop.title}
          onClick={onClick}
          targetKey={prop.key}
          onDelete={() => onUnAttach(prop)}
          isAttach={!!prop.object}
        >
          <ValueContent prop={prop} onUpdate={onUpdate} entity={entity} />
        </ValueItemContainer>
      ))}
    </>
  );
};

const ReadProperty: React.FC<{
  entity: Entity | BaseGui | BaseDom;
  properties: IProperty[];
}> = ({ entity, properties }) => {
  const [openLink, setOpenLink] = useState(false);
  const [targetKey, setTargetKey] = useState("");

  const [list, setList] = useState([]);
  const startLink = (evt: React.SyntheticEvent) => {
    const targetKey = evt.currentTarget.getAttribute("data-key");
    setTargetKey(targetKey);
    setOpenLink(true);
  };
  const endLink = () => {
    setOpenLink(false);
  };
  const handleConfirm = (db) => {
    setList((list) => {
      for (const l of list) {
        if (l.targetKey === targetKey) {
          l.object = db;
        }
      }
      return [...list];
    });
    endLink();
  };
  const handleUnAttach = (prop) => {
    if (prop.object) App.intance.Database.DataBindTable.remove(prop.object);
    prop.object = null;
    setList([...list]);
  };

  const updateProps = () => {
    setList([...list]);
  };
  useEffect(() => {
    if (properties) {
      const list = [];
      for (const prop of properties) {
        // if (prop.type === "number") {
        const dbi = getTargetDataBind(entity, prop.key);
        list.push({ ...prop, object: dbi, targetKey: prop.key });
        // }
      }
      setList(list);
    }
  }, [properties]);

  return (
    <Box>
      <ValueList
        props={list}
        entity={entity}
        onClick={startLink}
        onUnAttach={handleUnAttach}
        onUpdate={updateProps}
      />
      <Drawer anchor="right" open={openLink} onClose={endLink}>
        <DataBinding
          onClose={endLink}
          entity={entity}
          targetKey={targetKey}
          onConfirm={handleConfirm}
        />
      </Drawer>
    </Box>
  );
};
export default ReadProperty;
