import { Entity } from "@hc/engine";
import { IconButton, Button } from "@mui/material";
import { observer } from "mobx-react-lite";
import React, { useEffect } from "react";
import {
  EditorIntMode,
  IInterActDataOption,
  IInteractionActionOption,
  InteractionStore,
  interactionStore,
} from "~/stores/InteractionStore";

import { StyledTreeItem } from "../common/customTree";
import SpaceAddHint from "../common/spaceAddHint";
import { InteractionConfig } from "./config";
import ArrowDownwardIcon from "@mui/icons-material/ArrowDownward";
import ArrowUpwardIcon from "@mui/icons-material/ArrowUpward";

interface ActionsTreeItemProps {
  actions: IInteractionActionOption[];
  onTogglePopover: (evt) => void;
  onClick: (index: number, actIndex: number) => void;
  newAddId?: string;
  onRef?: (el: HTMLElement, id: string) => void;
  store: InteractionStore;
  onEnter?: (evt) => void;
}

const EndElement = ({
  actions,
  onTogglePopover,
  onMouseDown,
  act,
  isNew,
  onEnter,
}) => {
  let index = actions.findIndex((item) => item === act);
  const handleUp = () => {
    interactionStore.handleUp(actions, index);
  };
  const handleDown = () => {
    interactionStore.handleDown(actions, index);
  };
  const btnRef = React.useRef<HTMLButtonElement>(null);

  useEffect(() => {
    if (btnRef.current && isNew) {
      btnRef.current.click();
    }
  }, []);

  return (
    <>
      {act.type !== "Wait" && (
        <Button
          ref={btnRef}
          onMouseDown={onMouseDown}
          onClick={onTogglePopover}
          data-page={act.type === "Dispatch" ? "1" : "0"}
          data-type={act.param ? "param" : undefined}
          onMouseEnter={onEnter}
        >
          {act.target ? "" : ""}
        </Button>
      )}
      <IconButton onClick={handleUp}>
        <ArrowUpwardIcon />
      </IconButton>
      <IconButton onClick={handleDown}>
        <ArrowDownwardIcon />
      </IconButton>
    </>
  );
};
const WaitEndElement = ({ actions, act }) => {
  let index = actions.findIndex((item) => item === act);
  const handleUp = () => {
    interactionStore.handleUp(actions, index);
  };
  const handleDown = () => {
    interactionStore.handleDown(actions, index);
  };

  return (
    <>
      <IconButton onClick={handleUp}>
        <ArrowUpwardIcon />
      </IconButton>
      <IconButton onClick={handleDown}>
        <ArrowDownwardIcon />
      </IconButton>
    </>
  );
};
const ActionsTreeItem: React.FC<ActionsTreeItemProps> = ({
  actions,
  onTogglePopover,
  onClick,
  newAddId,
  onRef,
  store,
  onEnter,
}) => {
  const getItemLabel = (
    act: IInteractionActionOption,
    d: IInterActDataOption
  ) => {
    if (InteractionConfig[act.type]?.getDesc) {
      return InteractionConfig[act.type]?.getDesc(d);
    }

    if (act.target) {
      return `${act.label} ${
        d.objectId ? (d.objectId.Object as Entity)?.Name : ""
      }`;
    }
    if (act.param) {
      return `${d.key} " "`;
    }
    return act.label;
  };

  const handleAdd = () => {
    onClick(undefined, undefined);
    store.step = 2;
  };

  if (
    actions.length === 0 &&
    store.step !== 2 &&
    store.mode === EditorIntMode.List
  ) {
    return (
      <SpaceAddHint onClick={handleAdd} title="?" />
    );
  }

  return (
    <>
      {actions.map((act, actIndex) => (
        <StyledTreeItem
          key={act.id}
          nodeId={act.id.toString()}
          labelText={act.label}
          ref={(el: HTMLElement) => onRef?.(el, act.id)}
          onClick={() => {
            onClick(undefined, actIndex);
          }}
          endElement={
            <EndElement
              actions={actions}
              act={act}
              isNew={act.id === newAddId}
              onMouseDown={() => onClick(undefined, actIndex)}
              onTogglePopover={onTogglePopover}
              onEnter={onEnter}
            />
          }
        >
          {act.data.map((d, index) => (
            <StyledTreeItem
              onClick={() => {
                onClick(index, actIndex);
              }}
              key={d.objectId?.Index ?? index}
              nodeId={`${act.id}-${(d.objectId?.Index ?? index).toString()}`}
              labelText={getItemLabel(act, d)}
            ></StyledTreeItem>
          ))}
        </StyledTreeItem>
      ))}
      {store.mode === EditorIntMode.List && store.step !== 2 && (
        <Button onClick={handleAdd}></Button>
      )}
    </>
  );
};
export default observer(ActionsTreeItem);
