import { Entity, BaseGui, BaseDom } from "@hc/engine";
import { App } from "~/intance";

export function getTargetDataBind(
  entity: Entity | BaseGui | BaseDom,
  targetKey: string
) {
  const list = App.intance.Database.DataBindTable.list;
  return list.find(
    (d) => d.target.Index === entity.Id && d.targetKey === targetKey
  );
}
