import { Box, Typography } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import ActionListItem from "./actionListItem";

interface ActionListPanelProps {}

const ActionListPanel: React.FC<ActionListPanelProps> = () => {
  const store = useInteractionStoresContext();

  return (
    <Box p="8px">
      <Typography>2.</Typography>
      {store.interactions.map((action, index) => (
        <ActionListItem interaction={action} key={index} eventIndex={index} />
      ))}
    </Box>
  );
};
export default observer(ActionListPanel);
